var PMSKit =  {
    // 日期(容器, 型態:單天Single/區間Range/單天時間SingleTime, 日期格式)
    DatePicker: function (_selector, _type='Single', _format='YYYY-MM-DD', _sd=new Date(), _ed=new Date()){
        _selector.daterangepicker({
            singleDatePicker: ((_type=="Range")?false:true),
            autoUpdateInput: false,
            opens: "center",
            drops: "auto",
            showCustomRangeLabel:false,
            timePicker:((_type=="SingleTime")?true:false),
            timePicker24Hour: ((_type=="SingleTime")?true:false),
            locale: {
                "format": _format,
                "daysOfWeek": ["日", "一", "二", "三", "四", "五", "六"],
                "monthNames": ["1月", "2月", "3月", "4月", "5月", "6月","7月", "8月", "9月", "10月", "11月", "12月"],
                "firstDay": 1,
                "applyLabel": "確定",
                "cancelLabel": "清除",
                "separator": "~"
            }
        });
        _selector.on('apply.daterangepicker', function(ev, picker) {
            if (_type=="Range"){
                $(this).val(picker.startDate.format(_format).toString() + '~' + picker.endDate.format(_format).toString());
            }else{
                $(this).val(picker.startDate.format(_format).toString());
            }
        });
        _selector.on("cancel.daterangepicker", function (ev, picker) {
            $(this).val("");
        });
    },
    // 選項選取(容器, 型態:單選 Single/複選 Multiple)
    OptionSelect: function(_selector, _type='Single'){
        if (_type=="Multiple"){
            _selector.attr('multiple', 'multiple');
        }
        _selector.ui_choose();
    },
    // 金額格式化
    MoneyFormat: function(_value){
        const spec = new Intl.NumberFormat( 'zh-TW', {
            useGrouping: true
        } );
        return spec.format(_value);
    }
}
export default PMSKit;