import { data } from "jquery";

// 取得Token 並加入 Authorization 作為共用使用 Header
class HeadersConfig {
    MyController = "Header"; //API上的
    
    // 取得驗證 Header
    TokenHeaders(ContentType) {
        var AuthToken = localStorage.getItem("AuthToken") || "";//Token
        ContentType = ContentType || "application/json";
        const Config = {
            headers: {
                "Content-type": ContentType,
                "Authorization": `Bearer ${AuthToken}`
            },
            data: {}
        };
        return Config;
    }
    TokenDownloadHeaders() {
        var AuthToken = localStorage.getItem("AuthToken") || "";//Token
        const Config = {
            headers: {
                "Authorization": `Bearer ${AuthToken}`
            },
            responseType: "blob"
        };
        return Config;
    }
    LoginTokenHeaders() {
        var LoginAuthToken = sessionStorage.getItem("LoginAuthToken") || "";//Token
        const Config = {
            headers: {
                "Content-type": "application/json",
                "Authorization": `Bearer ${LoginAuthToken}`
            }
        };
        return Config;
    }
}

export default new HeadersConfig();