import http from "./HttpAxios";
import HeadersConfig from "./HeadersConfig";                                        // 取得驗證Header
import { BraMenuClass } from '@/Models/BraSysMenu/BraSysMenu.model';                // 選單資料Model

class LeftMenuService {
    MyMenuClass = new BraMenuClass();
    MyController = "BraUsers";

    List(StoreBarnch) {
        const Config = HeadersConfig.TokenHeaders();
        //const ItemId = this.$route.path;
        const StoreNo = StoreBarnch || "";

        http.get(`/${this.MyController}/AuthedMenu?StoreNo=${StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyMenuClass = response.data;
            //console.log(`/MgrMenuItem/LeftItemList?ItemId=${ItemId}`);
        }).catch(e => {
            console.log("選單讀取有誤:" + e);
        });
    }
}

export default new LeftMenuService();