</template>
<script>
    import AuthService from "@/Services/Auth.service";
    import SysDataService from "@/Services/SysData.service";
    import PMSKitHelper from "@/Helper/PMSKit.helper.js";
    import OrdersStayService from "@/Services/OrdersStay.service";
    import { BraOrdersForm, BraOrdersRoomType, BraOrdersRooms, BraOrdersRoomsDate, BraOrdersRoomsAddBuy, BraOrdersCustomer, BraOrdersCustomerTag } from "@/Models/BraOrders/BraOrders.model";
    import moment from "moment";

    export default {
        name: "OrdersStayAdd",
        data() {
            return {
                Auth: AuthService,                      // 驗證服務
                PMSConfig: SysDataService,              // PMS系統配置
                PMSKit: PMSKitHelper,                   // PMS套件
                Service: OrdersStayService,             // 模組資料服務
                DialogStoreTagSelector: null,           // 標籤燈箱選擇器
                DialogProjects_SearchDate: moment().format("MM-DD") + "~" + moment().add(1, 'days').format("MM-DD"),    // 促銷專案燈箱:搜尋日期
                DialogProjects_SearchSDate: moment().format("YYYY-MM-DD"),                                              // 促銷專案燈箱:搜尋開始日期
                DialogProjects_SearchEDate: moment().add(1, 'days').format("YYYY-MM-DD"),                               // 促銷專案燈箱:搜尋結束日期
                DialogProjects_SearchDateAmount: "1",    // 促銷專案燈箱:搜尋天數
                DialogProjects_SearchRoomType: "",       // 促銷專案燈箱:搜尋房型
                DialogProjects_SearchRoomTypeTxt: "",    // 促銷專案燈箱:搜尋房型文字
                DialogProjects_RangeDate: [],            // 促銷專案燈箱:區間日期
                DialogProjects_Building: [],             // 促銷專案燈箱:館別
                DialogProjects_Floor: [],                // 促銷專案燈箱:樓層
                DialogProjects_RoomType: [],             // 促銷專案燈箱:房型
                DialogProjects_Select: [],               // 促銷專案燈箱:可選擇專案
                DialogProjects_Selected: [],             // 促銷專案燈箱:已選擇專案
            }
        },
        methods: {
            // 取消:送出
            CancelOpen(){
                this.$router.push({ path: "/" + this.$route.params.branch + "/consoles", query: {  } });
            },
            // 回列表
            ListOpen(){
                this.$router.push({ path: "/" + this.$route.params.branch + "/consoles/Orders/list", query: {  } });
            },
            // 新增:送出
            AddSubmit() {
                if (this.Auth.MyBraUsersPower.BraPA != 1) {
                    alert("沒有新增權限");
                    confirm('')
                    return false;
                }

                this.Service.MyFormData.BraOrdersState = 1;
                this.Service.MyFormData.Booker.BraOrdersCustomerType = 1;
                console.log(this.Service.MyFormData);
            },
            // 訂單表單:房間 > 刪除
            OrdersFormRoomsDelete(rtIndex, roIndex){
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList.splice(roIndex, 1);
                
                if (this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList.length==0){
                    this.Service.MyFormData.RoomTypeList.splice(rtIndex, 1);
                }

                if (this.Service.MyFormData.RoomTypeList.length==0){
                    this.Service.MyFormData = new BraOrdersForm();
                }else{
                    this.OrdersFormTotalUpdate();
                }
            },
            // 訂單表單:入住/退房時間 > 開啟
            OrdersFormCheckinTimeOpen(ev){
                console.log(ev);
            },
            // 訂單表單:加購 > 新增
            OrdersFormAddBuyAdd(rtIndex, roIndex){
                let abItem = new BraOrdersRoomsAddBuy();
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList.push(abItem);

                $("#AddBuyList_" + rtIndex + "_" + roIndex).collapse('show');
            },
            // 訂單表單:加購 > 編輯
            OrdersFormAddBuyModify(rtIndex, roIndex, abIndex, sabIndex){
                let abItemData = this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList[abIndex];
                let abItem = new BraOrdersRoomsAddBuy();
                if (sabIndex!=""){
                    abItem.BraStoreAddBuyNo = this.Service.MyStoreAddBuyData.Result[sabIndex-1].Val;
                    abItem.BraStoreAddBuyName = this.Service.MyStoreAddBuyData.Result[sabIndex-1].Txt;
                    abItem.BraOrdersRoomsAddBuyAmount = parseInt(abItemData.BraOrdersRoomsAddBuyAmount);
                    abItem.BraOrdersRoomsAddBuyPrice = parseInt(this.Service.MyStoreAddBuyData.Result[sabIndex-1].Price);
                    abItem.BraOrdersRoomsAddBuySbutotal = parseInt(abItemData.BraOrdersRoomsAddBuyAmount) * parseInt(this.Service.MyStoreAddBuyData.Result[sabIndex-1].Price);
                    abItem.BraOrdersRoomsAddBuyState = 0;
                }
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList[abIndex] = abItem;

                if (parseInt(this.Service.MyFormData.BraOrdersSubTotal) > 0){
                    this.OrdersFormTotalUpdate();
                }
            },
            // 訂單表單:加購 > 編輯數量
            OrdersFormAddBuyModifyAmount(rtIndex, roIndex, abIndex, sabAmount){
                let abItemData = this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList[abIndex];
                let abItem = new BraOrdersRoomsAddBuy();
                abItem.BraStoreAddBuyNo = abItemData.BraStoreAddBuyNo;
                abItem.BraStoreAddBuyName = abItemData.BraStoreAddBuyName;
                abItem.BraOrdersRoomsAddBuyAmount = parseInt(sabAmount);
                abItem.BraOrdersRoomsAddBuyPrice = parseInt(abItemData.BraOrdersRoomsAddBuyPrice);
                abItem.BraOrdersRoomsAddBuySbutotal = parseInt(sabAmount) * parseInt(abItemData.BraOrdersRoomsAddBuyPrice);
                abItem.BraOrdersRoomsAddBuyState = 0;
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList[abIndex] = abItem;

                if (parseInt(this.Service.MyFormData.BraOrdersSubTotal) > 0){
                    this.OrdersFormTotalUpdate();
                }
            },
            // 訂單表單:加購 > 數量更改
            OrdersFormAddBuyAmountChange(rtIndex, roIndex, abIndex, changeAmount){
                let abItemData = this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList[abIndex];
                let sabAmount = parseInt(abItemData.BraOrdersRoomsAddBuyAmount) + changeAmount;
                if (sabAmount > -1){
                    this.OrdersFormAddBuyModifyAmount(rtIndex, roIndex, abIndex, sabAmount);
                }
            },
            // 訂單表單:加購 > 刪除
            OrdersFormAddBuyDelete(rtIndex, roIndex, abIndex){
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList.splice(abIndex, 1);

                if (this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList.length==0){
                    let abItem = new BraOrdersRoomsAddBuy();
                    this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].AddBuyList.push(abItem);
                }

                this.OrdersFormTotalUpdate();
            },
            // 訂單表單:總額更新
            OrdersFormTotalUpdate(){
                let OToday = 0;
                let ORAmount = 0;
                let OSubTotal = 0;
                let ODiscount = 0;
                let OBonus = 0;
                let OTotal = 0;

                $.each(this.Service.MyFormData.RoomTypeList, function(rt, rtValue) {
                    let rodaTotal = 0;
                    $.each(rtValue.RoomsList, function(ro, roValue) { 
                        let daTotal = 0;
                        $.each(roValue.DateList, function(da, daValue) { 
                            daTotal += parseInt(daValue.BraOrdersRoomsDatePrice);
                        });
                        roValue.DateTotal = daTotal;

                        if (parseInt(roValue.DateTotal) > 0){
                            let abTotal = 0;
                            $.each(roValue.AddBuyList, function(ab, abValue) {
                                if (abValue.BraStoreAddBuyNo!=''){
                                    abTotal += parseInt(abValue.BraOrdersRoomsAddBuySbutotal);
                                } 
                            });
                            roValue.AddBuyTotal = abTotal;
                            
                            if (roValue.BraOrdersRoomsSDate==moment().format("YYYY-MM-DD")){
                                OToday = 1;
                            }

                            ORAmount += 1;
                            OSubTotal += parseInt(roValue.DateTotal) + parseInt(roValue.AddBuyTotal);
                            rodaTotal += parseInt(daTotal);
                        }
                    });
                    rtValue.RoomsTotalDate = parseInt(rodaTotal);
                });
                OTotal = OSubTotal - ODiscount - OBonus;

                this.Service.MyFormData.BraOrdersToday = OToday;
                this.Service.MyFormData.BraOrdersRoomsAmount = ORAmount;
                this.Service.MyFormData.BraOrdersSubTotal = OSubTotal;
                this.Service.MyFormData.BraOrdersDiscount = ODiscount;
                this.Service.MyFormData.BraOrdersBonus = OBonus;
                this.Service.MyFormData.BraOrdersTotal = OTotal;
                this.Service.MyFormData.BraOrdersReceiveTotal = OTotal;
                this.Service.MyFormData.BraOrdersDeposit = Math.round(OTotal * (parseInt(this.PMSConfig.MyStoreSettings.BraStoreDepositRatio) / 100));
                this.Service.MyFormData.BraOrdersDepositEDate = moment().add(this.PMSConfig.MyStoreSettings.BraStoreDepositDay, 'days').format("YYYY-MM-DD");
                this.Service.MyFormData.BraOrdersReceived = 0;
            },
            // 訂單表單:訂房者/住客-國別 > 更改
            OrdersFormCountryTypeChange(selector, ctypeValue){
                if (ctypeValue==1){
                    selector.BraCountryNo = 1;
                }else{
                    selector.BraCountryNo = 0;
                }
                selector.BraCityNo = 0;
                selector.BraTownNo = 0;
            },
            // 訂單表單:訂房者/住客-國家 > 更改
            OrdersFormCountryChange(selector){
                selector.BraCityNo = 0;
                selector.BraTownNo = 0;
            },
            // 訂單表單:訂房者/住客-縣市 > 更改
            OrdersFormCityChange(selector){
                selector.BraTownNo = 0;
            },
            // 訂單表單:住客資料 > 複製
            OrdersFormCustomerCopy(selector){
                selector.BraMemberNo = this.Service.MyFormData.Booker.BraMemberNo;
                selector.BraOrdersCustomerSurame = this.Service.MyFormData.Booker.BraOrdersCustomerSurame;
                selector.BraOrdersCustomerGivennName = this.Service.MyFormData.Booker.BraOrdersCustomerGivennName;
                selector.BraOrdersCustomerTel = this.Service.MyFormData.Booker.BraOrdersCustomerTel;
                selector.BraOrdersCustomerMobile = this.Service.MyFormData.Booker.BraOrdersCustomerMobile;
                selector.BraOrdersCustomerSex = this.Service.MyFormData.Booker.BraOrdersCustomerSex;
                selector.BraOrdersCustomerBirthday = this.Service.MyFormData.Booker.BraOrdersCustomerBirthday;
                selector.BraOrdersCustomerEmail = this.Service.MyFormData.Booker.BraOrdersCustomerEmail;
                selector.BraOrdersCustomerCar = this.Service.MyFormData.Booker.BraOrdersCustomerCar;
                selector.BraOrdersCustomerMobileBarcode = this.Service.MyFormData.Booker.BraOrdersCustomerMobileBarcode;
                selector.BraOrdersCustomerIDNumber = this.Service.MyFormData.Booker.BraOrdersCustomerIDNumber;
                selector.BraOrdersCustomerCountryType = this.Service.MyFormData.Booker.BraOrdersCustomerCountryType;
                selector.BraCountryNo = this.Service.MyFormData.Booker.BraCountryNo;
                selector.BraCityNo = this.Service.MyFormData.Booker.BraCityNo;
                selector.BraTownNo = this.Service.MyFormData.Booker.BraTownNo;
                selector.BraOrdersCustomerAddr = this.Service.MyFormData.Booker.BraOrdersCustomerAddr;
                selector.TagList = JSON.parse(JSON.stringify(this.Service.MyFormData.Booker.TagList));
            },
            // 訂單表單:住客 > 新增
            OrdersFormCustomerAdd(rtIndex, roIndex){
                let cuItem = new BraOrdersCustomer();
                cuItem.BraOrdersCustomerType = 3;
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].CustomerList.push(cuItem);
            },
            // 訂單表單:住客 > 刪除
            OrdersFormCustomerDelete(rtIndex, roIndex, cuIndex){
                this.Service.MyFormData.RoomTypeList[rtIndex].RoomsList[roIndex].CustomerList.splice(cuIndex, 1);
            },
            // 通路來源燈箱:開啟
            DialogStoreChannelOpen(){
                $("#DialogStoreChannel .ui-choose li.selected").removeClass('selected');
                if (this.Service.MyFormData.BraStoreChannel!=""){
                    $("#DialogStoreChannel .ui-choose li[value=" + this.Service.MyFormData.BraStoreChannel + "]").addClass('selected');
                }

                $("#DialogStoreChannel").modal('show');
            },
            // 通路來源燈箱:確認
            DialogStoreChannelSubmit(){
                let selVal = "", selTxt = "";
                if ($("#DialogStoreChannel .ui-choose li.selected").length > 0){
                    selVal = $("#DialogStoreChannel .ui-choose li.selected").attr("value");
                    selTxt = $("#DialogStoreChannel .ui-choose li.selected").text();
                }
                this.Service.MyFormData.BraStoreChannel = selVal;
                this.Service.MyFormData.BraStoreChannelTxt = selTxt;
            },
            // 標籤燈箱:開啟
            DialogStoreTagOpen(Selector){
                this.DialogStoreTagSelector = Selector;

                $("#DialogStoreTag .ui-choose li.selected").removeClass('selected');
                if (Selector.TagList.length > 0){
                    $.each(Selector.TagList, function(index, value) { 
                        $("#DialogStoreTag .ui-choose li[value=" + value.BraStoreTagNo + "]").addClass('selected');
                    });
                }

                $("#DialogStoreTag").modal('show');
            },
            // 標籤燈箱:確認
            DialogStoreTagSubmit(){
                let SelectList = [];
                if ($("#DialogStoreTag .ui-choose li.selected").length > 0){
                    $("#DialogStoreTag .ui-choose li.selected").each(function (i) {
                        if (parseInt($(this).attr("value")) > 0) {
                            let cuItem = new BraOrdersCustomerTag();
                            cuItem.BraStoreTagNo = $(this).attr("value");
                            cuItem.BraStoreTagName = $(this).text();
                            SelectList.push(cuItem);
                        }
                    });
                }
                this.DialogStoreTagSelector.TagList = SelectList;
            },
            // 房型燈箱:開啟
            DialogRoomTypeOpen(){
                $("#DialogRoomType .ui-choose li.selected").removeClass('selected');

                if (this.DialogProjects_SearchRoomType!=""){
                    let SelectList = this.DialogProjects_SearchRoomType.split(',');
                    $.each(SelectList, function(index, value) { 
                        $("#DialogRoomType .ui-choose li[value=" + value + "]").addClass('selected');
                    });
                }else{
                    $("#DialogRoomType .ui-choose li[value=0]").addClass('selected');
                }

                $("#DialogRoomType").modal('show');
            },
            // 房型燈箱:確認
            DialogRoomTypeSubmit(){
                let selVal = "", selTxt = "";
                if ($("#DialogRoomType .ui-choose li.selected").length > 0){
                    $("#DialogRoomType .ui-choose li.selected").each(function (i) {
                        if (parseInt($(this).attr("value")) > 0) {
                            if (selVal!="") selVal += ",";
                            selVal += $(this).attr("value");
                            if (selTxt!="") selTxt += "、";
                            selTxt += $(this).text();
                        }
                    });
                }
                this.DialogProjects_SearchRoomType = selVal;
                this.DialogProjects_SearchRoomTypeTxt = selTxt;
            },
            // 房型燈箱:選擇互斥
            DialogRoomTypeSelect(IsAll){
                if (IsAll){
                    $("#DialogRoomType .ui-choose li.selected").not("li[value=0]").removeClass("selected");
                }else{
                    $("#DialogRoomType .ui-choose li[value=0].selected").removeClass("selected");

                }
            },
            // 促銷專案燈箱:開啟
            DialogProjectsOpen(){
                this.DialogProjects_SearchDate = moment().format("MM-DD") + "~" + moment().add(1, 'days').format("MM-DD");
                this.DialogProjects_SearchSDate = moment().format("YYYY-MM-DD");
                this.DialogProjects_SearchEDate = moment().add(1, 'days').format("YYYY-MM-DD");
                this.DialogProjects_SearchDateAmount = 1;
                this.DialogProjects_SearchRoomType = "";
                this.DialogProjects_SearchRoomTypeTxt = "";
                this.DialogProjects_RangeDate = [];
                this.DialogProjects_Building = [];
                this.DialogProjects_Floor = [];
                this.DialogProjects_RoomType = [];
                this.DialogProjects_Select = [];
                this.DialogProjects_Selected = [];
                
                this.DialogProjects_Selected = JSON.parse(JSON.stringify(this.Service.MyFormData.RoomTypeList));
                if (parseInt(this.Service.MyFormData.BraOrdersSubTotal)==0){
                    this.DialogProjects_Selected.splice(0, 1);
                }else{
                    let sd = moment(this.DialogProjects_Selected[0].RoomsList[0].BraOrdersRoomsSDate);
                    let ed = moment(this.DialogProjects_Selected[0].RoomsList[0].BraOrdersRoomsEDate);
                    let ad = moment.duration(ed.diff(sd)).asDays();
                    this.DialogProjects_SearchDate = sd.format("MM-DD") + "~" + ed.format("MM-DD");
                    this.DialogProjects_SearchSDate = sd.format("YYYY-MM-DD");
                    this.DialogProjects_SearchEDate = ed.format("YYYY-MM-DD");
                    this.DialogProjects_SearchDateAmount = ad;
                }
                
                $("#chooRNbModal").modal('show');
            },
            // 促銷專案燈箱:確認
            DialogProjectsSubmit(){
                if (this.DialogProjects_Selected.length==0){
                    this.Service.MyFormData = new BraOrdersForm();
                }else{
                    this.Service.MyFormData.RoomTypeList = JSON.parse(JSON.stringify(this.DialogProjects_Selected));
                    this.OrdersFormTotalUpdate();
                }
            },
            // 促銷專案燈箱:搜尋
            DialogProjectsSearch(){
                let vthis = this;
                let ERRORNo = 0;
                let ERROTxt = "";
                if (this.DialogProjects_SearchDate=="" || this.DialogProjects_SearchSDate=="" || this.DialogProjects_SearchEDate==""){
                    ERRORNo += 1;
                    ERROTxt += "請選擇入住日期<br>";
                }
                if (ERRORNo > 0) {
                    alert(ERROTxt);
                    return false;
                }else{
                    this.DialogProjects_RangeDate = [];
                    this.DialogProjects_Building = [];
                    this.DialogProjects_Floor = [];
                    this.DialogProjects_RoomType = [];
                    this.DialogProjects_Select = [];

                    $("#chooRNbModal #SearchBlock #FormLoading").show();
                }

                // API串接
                this.Service.ProjectsPlanData(this.DialogProjects_SearchSDate, this.DialogProjects_SearchEDate, this.DialogProjects_SearchRoomType).then(response => {
                    //console.log(response.data);
                    if (response.data.Status == "SUCCESS") {
                        this.DialogProjects_RangeDate = response.data.DateList;

                        this.$nextTick(() => {
                            if (this.DialogProjects_RangeDate.length > 0 && this.DialogProjects_RangeDate[0].BuildingList.length > 0){
                                $("#chooRNbModal #SearchBlock #DataDateAll").click();
                            }else{
                                $("#chooRNbModal #SearchBlock #DataDateList li").eq(0).click();
                            }
                        });
                    } else {
                        alert(response.data.Message);
                    }
                }).catch(() => {
                    alert(`促銷專案方案房型取得失敗:ProjectsPlanData`);
                }).finally(() => {
                    $("#chooRNbModal #SearchBlock #FormLoading").hide();
                });
            },
            // 促銷專案燈箱:搜尋結果 > 日期
            DialogProjectsDateOpen(daIndex){
                this.DialogProjects_Building = this.DialogProjects_RangeDate[daIndex].BuildingList;
                this.DialogProjects_Floor = [];
                this.DialogProjects_RoomType = [];

                $("#chooRNbModal #SearchBlock #DataDateAll").removeClass("bgcOdMulDay");
                $("#chooRNbModal #SearchBlock #DataDateList li.active").removeClass("active");
                if (daIndex==0){
                    $("#chooRNbModal #SearchBlock #DataDateAll").addClass("bgcOdMulDay");
                }else{
                    $("#chooRNbModal #SearchBlock #DataDateList li").eq(daIndex - 1).addClass("active");
                }
                $("#chooRNbModal #SearchBlock #DataBuildingList label.active").removeClass("active");
                $("#chooRNbModal #SearchBlock #DataFloorList label.active").removeClass("active"); 
                $("#chooRNbModal #SearchBlock #DataRoomTypeList li.selected").removeClass("selected"); 

                if (this.DialogProjects_Building.length==1){
                    this.$nextTick(() => {
                        $("#chooRNbModal #SearchBlock #DataBuildingList label").eq(0).click();
                    });
                }
            },
            // 促銷專案燈箱:搜尋結果 > 館別
            DialogProjectsBuildingOpen(buIndex){
                this.DialogProjects_Floor = this.DialogProjects_Building[buIndex].FloorList;
                this.DialogProjects_RoomType = this.DialogProjects_Building[buIndex].RoomTypeList;

                $("#chooRNbModal #SearchBlock #DataBuildingList label.active").removeClass("active"); 
                $("#chooRNbModal #SearchBlock #DataBuildingList label").eq(buIndex).addClass("active");
                $("#chooRNbModal #SearchBlock #DataFloorList label.active").removeClass("active"); 
                $("#chooRNbModal #SearchBlock #DataRoomTypeList li.selected").removeClass("selected");
                $.each(this.DialogProjects_RoomType, function(rt, rtValue) { 
                    let selIndex = $("#chooRNbModal #SelectBlock ul[val-RDate='" + rtValue.RangeDate + "'][val-SBNo='" + rtValue.BraStoreBuildingNo + "'][val-SFNo='" + rtValue.BraStoreFloorNo + "'][val-RTNo='" + rtValue.BraRoomTypeNo + "']").attr("val-index");
                    if (selIndex!="" && selIndex>=0) $("#chooRNbModal #SearchBlock #DataRoomTypeList li").eq(rt).addClass("selected");
                })
            },
            // 促銷專案燈箱:搜尋結果 > 樓層
            DialogProjectsFloorOpen(flIndex){
                this.DialogProjects_RoomType = this.DialogProjects_Floor[flIndex].RoomTypeList;

                $("#chooRNbModal #SearchBlock #DataFloorList label.active").removeClass("active"); 
                if ($("#chooRNbModal #SearchBlock #DataFloorList label").eq(flIndex).hasClass("active")){
                    $("#chooRNbModal #SearchBlock #DataFloorList label.active").removeClass("active"); 
                    let buIndex = $("#chooRNbModal #SearchBlock #DataBuildingList label").index($("#chooRNbModal #SearchBlock #DataBuildingList label.active"));
                    this.DialogProjectsBuildingOpen(buIndex);
                }else{
                    $("#chooRNbModal #SearchBlock #DataFloorList label.active").removeClass("active"); 
                    $("#chooRNbModal #SearchBlock #DataFloorList label").eq(flIndex).addClass("active");
                }
                $("#chooRNbModal #SearchBlock #DataRoomTypeList li.selected").removeClass("selected"); 
                $.each(this.DialogProjects_RoomType, function(rt, rtValue) { 
                    let selIndex = $("#chooRNbModal #SelectBlock ul[val-RDate='" + rtValue.RangeDate + "'][val-SBNo='" + rtValue.BraStoreBuildingNo + "'][val-SFNo='" + rtValue.BraStoreFloorNo + "'][val-RTNo='" + rtValue.BraRoomTypeNo + "']").attr("val-index");
                    if (selIndex!="" && selIndex>=0) $("#chooRNbModal #SearchBlock #DataRoomTypeList li").eq(rt).addClass("selected");
                })
            },
            // 促銷專案燈箱:搜尋結果 > 房型
            DialogProjectsRoomTypeOpen(rtIndex, rtAmount){
                if (parseInt(rtAmount) > 0){
                    if ($("#chooRNbModal #SearchBlock #DataRoomTypeList li").eq(rtIndex).hasClass("selected")){
                        let selIndex = $("#chooRNbModal #SelectBlock ul[val-RDate='" + this.DialogProjects_RoomType[rtIndex].RangeDate + "'][val-SBNo='" + this.DialogProjects_RoomType[rtIndex].BraStoreBuildingNo + "'][val-SFNo='" + this.DialogProjects_RoomType[rtIndex].BraStoreFloorNo + "'][val-RTNo='" + this.DialogProjects_RoomType[rtIndex].BraRoomTypeNo + "']").attr("val-index");
                        this.DialogProjects_Select.splice(selIndex, 1);

                        $("#chooRNbModal #SearchBlock #DataRoomTypeList li").eq(rtIndex).removeClass("selected");
                    }else{
                        this.DialogProjects_Select.push(this.DialogProjects_RoomType[rtIndex]);

                        $("#chooRNbModal #SearchBlock #DataRoomTypeList li").eq(rtIndex).addClass("selected");
                    }
                }
            },
            //促銷專案燈箱:可選擇專案 > 移除
            DialogProjectsSelectRemove(rtIndex){
                $("#chooRNbModal #SearchBlock #DataRoomTypeList li[val-RDate='" + this.DialogProjects_Select[rtIndex].RangeDate + "'][val-SBNo='" + this.DialogProjects_Select[rtIndex].BraStoreBuildingNo + "'][val-SFNo='" + this.DialogProjects_Select[rtIndex].BraStoreFloorNo + "'][val-RTNo='" + this.DialogProjects_Select[rtIndex].BraRoomTypeNo + "']").removeClass("selected");
                this.DialogProjects_Select.splice(rtIndex, 1);
            },
            // 促銷專案燈箱:可選擇專案 > 加入訂房
            DialogProjectsSelectAdd(rtIndex, plIndex){
                let DateChecked = $("#chooRNbModal #SelectBlock ul:eq(" + rtIndex + ") li#PlanList:eq(" + plIndex + ") input#DatePrice:checked");
                if (DateChecked.length==0){
                    alert("請勾選日期");
                    return false;
                }else{
                    let vthis = this;
                    let daList = [];
                    DateChecked.each(function() { 
                        let daIndex = $("#chooRNbModal #SelectBlock ul:eq(" + rtIndex + ") li#PlanList:eq(" + plIndex + ") input").index(this);
                        let daItem = new BraOrdersRoomsDate();
                        daItem.BraOrdersRoomsDateTxt = vthis.DialogProjects_Select[rtIndex].PlanList[plIndex].PriceList[daIndex].BraProjectsPlanDate;
                        daItem.BraOrdersRoomsDateWeek = vthis.DialogProjects_Select[rtIndex].PlanList[plIndex].PriceList[daIndex].BraProjectsPlanWeek;
                        daItem.BraOrdersRoomsDatePrice = vthis.DialogProjects_Select[rtIndex].PlanList[plIndex].PriceList[daIndex].BraProjectsPlanPrice;
                        daItem.BraOrdersRoomsDateState = 0;
                        daList.push(daItem);
                    });

                    let abList = [];
                    let abItem = new BraOrdersRoomsAddBuy();
                    abList.push(abItem);

                    let cuList = [];
                    let cuItem = new BraOrdersCustomer();
                    cuItem.BraOrdersCustomerType = 2;
                    cuList.push(cuItem);

                    let roList = [];
                    let roAmount = parseInt($("#chooRNbModal #SelectBlock ul:eq(" + rtIndex + ") li#PlanList:eq(" + plIndex + ") #SelAmount").val());
                    for (var i = 0; i < roAmount; i++){
                        let roItem = new BraOrdersRooms();
                        roItem.BraProjectsPlanNo = this.DialogProjects_Select[rtIndex].PlanList[plIndex].BraProjectsPlanNo;
                        roItem.BraProjectsName = this.DialogProjects_Select[rtIndex].PlanList[plIndex].BraProjectsName;
                        roItem.BraRoomsName = "";
                        roItem.BraOrdersRoomsSDate = daList[0].BraOrdersRoomsDateTxt;
                        roItem.BraOrdersRoomsEDate = moment(daList[(daList.length - 1)].BraOrdersRoomsDateTxt).add(1, 'days').format("YYYY-MM-DD");
                        roItem.BraOrdersRoomsCheckinSTime = "";
                        roItem.BraOrdersRoomsCheckinETime = "";
                        roItem.BraOrdersRoomsState = 0;
                        roItem.DateList = JSON.parse(JSON.stringify(daList));
                        roItem.AddBuyList = JSON.parse(JSON.stringify(abList));
                        roItem.CustomerList = JSON.parse(JSON.stringify(cuList));
                        roList.push(roItem);
                    }
                    
                    let seledIndex = 0;
                    let seled = $("#chooRNbModal #SelectedBlock ul[val-SBNo='" + this.DialogProjects_Select[rtIndex].BraStoreBuildingNo + "'][val-SFNo='" + this.DialogProjects_Select[rtIndex].BraStoreFloorNo + "'][val-RTNo='" + this.DialogProjects_Select[rtIndex].BraRoomTypeNo + "']");
                    if (seled.length==0){
                        seledIndex = this.DialogProjects_Selected.length;
                        let rtItem = new BraOrdersRoomType();
                        rtItem.BraStoreBuildingNo = this.DialogProjects_Select[rtIndex].BraStoreBuildingNo;
                        rtItem.BraStoreBuildingName = this.DialogProjects_Select[rtIndex].BraStoreBuildingName;
                        rtItem.BraStoreFloorNo = this.DialogProjects_Select[rtIndex].BraStoreFloorNo;
                        rtItem.BraStoreFloorName = this.DialogProjects_Select[rtIndex].BraStoreFloorName;
                        rtItem.BraRoomTypeNo = this.DialogProjects_Select[rtIndex].BraRoomTypeNo;
                        rtItem.BraRoomTypeName = this.DialogProjects_Select[rtIndex].BraRoomTypeName;
                        rtItem.RoomsList = roList;
                        this.DialogProjects_Selected.push(rtItem);
                    }else{
                        seledIndex = seled.attr("val-index");
                        this.DialogProjects_Selected[seledIndex].RoomsList = this.DialogProjects_Selected[seledIndex].RoomsList.concat(roList);
                    }

                    this.DialogProjectsTotalUpdate(seledIndex);
                }
            },
            //促銷專案燈箱:已選擇專案 > 移除
            DialogProjectsSelectedRemove(rtIndex){
                this.DialogProjects_Selected.splice(rtIndex, 1);
            },
            //促銷專案燈箱:已選擇專案日期 > 移除
            DialogProjectsSelectedDateRemove(rtIndex, roIndex, daIndex){
                this.DialogProjects_Selected[rtIndex].RoomsList[roIndex].DateList.splice(daIndex, 1);
                
                if (this.DialogProjects_Selected[rtIndex].RoomsList[roIndex].DateList.length==0){
                    this.DialogProjects_Selected[rtIndex].RoomsList.splice(roIndex, 1);

                    if (this.DialogProjects_Selected[rtIndex].RoomsList.length==0){
                        this.DialogProjects_Selected.splice(rtIndex, 1);
                    }
                }

                if (this.DialogProjects_Selected.length > 0){
                    this.DialogProjectsTotalUpdate(rtIndex);
                }
            },
            // 促銷專案燈箱:總額更新
            DialogProjectsTotalUpdate(rtIndex){
                let rodaTotal = 0;
                $.each(this.DialogProjects_Selected[rtIndex].RoomsList, function(ro, roValue) { 
                    let daTotal = 0;
                    $.each(roValue.DateList, function(da, daValue) { 
                        daTotal += parseInt(daValue.BraOrdersRoomsDatePrice);
                    });
                    roValue.DateTotal = daTotal;
                    
                    rodaTotal += parseInt(roValue.DateTotal);
                });
                this.DialogProjects_Selected[rtIndex].RoomsTotalDate = parseInt(rodaTotal);
            }
        },
        // 實體已建立，狀態與事件已初始化完成
        created() {
            // 取得目前商店編號
            this.PMSConfig.StoreNo = this.$route.params.branch;
            this.Service.StoreNo = this.$route.params.branch;
        },
        // 實體與掛載完成
        mounted() {
            let vthis = this;
            
            // 新增權限
            if (this.Auth.MyBraUsersPower.BraPA != 1) {
                alert("沒有新增權限");
                this.$router.push({ path: "/", replace: true });
            }

            // 訂單資料:通路來源 > 載入配置:選項選取
            this.PMSKit.OptionSelect($("#DialogStoreChannel .ui-choose"), 'Single');
            // 訂單資料:標籤 > 載入配置:選項選取
            this.PMSKit.OptionSelect($("#DialogStoreTag .ui-choose"), 'Multiple');
            // 訂單資料:房型 > 載入配置:選項選取
            this.PMSKit.OptionSelect($("#DialogRoomType .ui-choose"), 'Multiple');

            // 訂單資料 > 頁籤切換
            $("#OrderData input[name=OrderTab]").on('click', function(e) {
                $("#OrderData label#OrderTab").removeClass("js_pgIni"); 
                $("#OrderData label").not("label[for=" + $(this).attr('id') + "]").addClass("js_pgIni"); 
            });

            // 促銷專案燈箱:日期 > 載入日期套件
            this.PMSKit.DatePicker($("#chooRNbModal #SearchBlock #FormDate"), 'Range', 'MM-DD', moment(this.DialogProjects_SearchSDate).format("YYYY-MM-DD"), moment(this.DialogProjects_SearchEDate).format("YYYY-MM-DD"));
            $("#chooRNbModal #SearchBlock #FormDate").on('apply.daterangepicker', function(ev, picker) {
                let sd = new Date(picker.startDate);
                let ed = new Date(picker.endDate);
                let ad = Math.floor((ed.getTime() - sd.getTime()) / (24 * 3600 * 1000));

                vthis.DialogProjects_SearchDate = picker.startDate.format('MM-DD').toString() + "~" + picker.endDate.format('MM-DD').toString();
                vthis.DialogProjects_SearchSDate = picker.startDate.format('YYYY-MM-DD').toString();
                vthis.DialogProjects_SearchEDate = picker.endDate.format('YYYY-MM-DD').toString();
                vthis.DialogProjects_SearchDateAmount = ad;
            });
            $("#chooRNbModal #SearchBlock #FormDate").on("cancel.daterangepicker", function (ev, picker) {
                vthis.DialogProjects_SearchDate = "";
                vthis.DialogProjects_SearchSDate = "";
                vthis.DialogProjects_SearchEDate = "";
                vthis.DialogProjects_SearchDateAmount = "";
            });
                
            // 取得商店設定項目
            this.PMSConfig.StoreSettings();
            // 取得通路來源資料
            this.Service.StoreChannelData();
            // 取得加購資料
            this.Service.StoreAddBuyData();
            // 取得標籤資料
            this.Service.StoreTagData();
            // 取得房型資料
            this.Service.RoomTypeData();
            // 取得國家資料
            this.PMSConfig.CountryData();
            // 取得縣市資料
            this.PMSConfig.CityData();
            // 取得縣市資料
            this.PMSConfig.TownData();
            // 取得選項資料
            this.PMSConfig.OptionData();
        },
        // 當狀態被變動時，畫面已同步更新完成
        updated(){
            // 訂單資料:入住/退房時間 > 載入日期套件
            this.PMSKit.DatePicker($("input#CheckinTime"), 'SingleTime', 'YYYY-MM-DD HH:mm');
            // 訂單資料:訂/住房者生日 > 載入日期套件
            this.PMSKit.DatePicker($("input#OrdersCustomerBirthday"), 'Single', 'YYYY-MM-DD');
        },
        // 實體物件被銷毀完畢
        unmounted(){
            // 清空主畫面資料
            this.Service.MyFormData = new BraOrdersForm();
        }
    };
</script>
