<template>
    <p>新增訂單-休息</p>
</template>
<script>
    import AuthService from "@/Services/Auth.service";
    import SysDataService from "@/Services/SysData.service";
    import PMSKitHelper from "@/Helper/PMSKit.helper.js";
    import OrdersService from "@/Services/Orders.service";

    export default {
        name: "OrdersRestAdd",
        data() {
            return {
                Auth: AuthService,              // 驗證服務
                PMSConfig: SysDataService,      // PMS系統配置
                PMSKit: PMSKitHelper,           // PMS套件
                Service: OrdersService,         // 模組資料服務
            }
        },
        created() {
            // 取得目前商店編號
            this.Service.StoreNo = this.$route.params.branch;
        },
        methods: {
            // 新增:送出
            AddSubmit() {
                if (this.Auth.MyBraUsersPower.BraPA != 1) {
                    alert("沒有新增權限");
                    return false;
                }

                let ERRORNo = 0;
                let ERROTxt = "";
                if (this.Service.MyFormData.BraBlacklistName.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入姓名<br>";
                }
                if (this.Service.MyFormData.BraBlacklistSex < 1) {
                    ERRORNo += 1;
                    ERROTxt += "請選擇性別<br>";
                }
                if (this.Service.MyFormData.BraBlacklistReason.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入原因<br>";
                }
                if (this.Service.MyFormData.BraBlacklistAddDate.trim() == "") {
                    this.Service.MyFormData.BraBlacklistAddDate = moment().format("YYYY-MM-DD");
                }
                if (ERRORNo > 0) {
                    alert(ERROTxt);
                    return false;
                }
                this.Service.Create().then(response => {
                    this.Service.MyFormData = response.data;
                    if (this.Service.MyFormData.Status == "SUCCESS") {
                        alert("新增成功!");
                        $("#addOrders").modal("hide");
                        this.Service.List();
                    }
                }).catch(e => {
                    alert("新增失敗!");
                    console.log(e);
                });
            },
        },
        // 物件搭建好後執行
        mounted() {
            let vthis = this;
            
            // 新增權限
            if (this.Auth.MyBraUsersPower.BraPA != 1) {
                alert("沒有新增權限");
                this.$router.push({ path: "/", replace: true });
            }
        }
    };
</script>
