import http from "./HttpAxios";
import HeadersConfig from "./HeadersConfig";                                        // 取得驗證Header
import { DataPaginated } from "@/Models/ResultData.model";                          // 列表共用分頁結果
import { BraDataList } from "@/Models/BraSysData/BraSysData.model";                 // 共用資料Model
import { BraUsersDetail, BraUsersForm } from "@/Models/BraUsers/BraUsers.model";    // 使用者資料Model

class UsersService {
    MyController = "BraUsers";
    MyPaginatedData = new DataPaginated();          // 分頁承接資料
    MyDataResult = new BraDataList();               // 非分頁承接資料
    MyDetailData = new BraUsersDetail();            // 詳細資料
    MyFormData = new BraUsersForm();                // 表單資料
    
    // 搜尋欄位(獨立)
    StoreNo = "";
    SearchId = "";          // 編號
    SearchName = "";        // 姓名
    SearchTel = "";         // 電話
    SearchJTitle = "";      // 職稱
    SearchStatus = "";      // 人員狀態
    SearchSDate = "";       // 建立開始日期
    SearchEDate = "";       // 建立結束日期
    // 排序欄位
    OrderSort = 1;          // 根據API排序欄位進行預設
    OrderAD = 0;            // 根據API排序條件設定 0=ASC 1=DESC
    // 分頁參數
    PageIndex = 1;          // 預設第一頁
    PageAmount = 10;        // 每頁10筆
    PageJump = 1;           // 跳頁
    PageTotalA = 0;         // 總數量
    PageTotalP = 0;         // 總頁數
    PageFirstli = 0;        // 計算分頁暫存使用
    PageLastli = 0;         // 計算分頁暫存使用
    Pageli = [];            // 存放頁數
    QueryUri = "";

    // 列表
    ListData() {
        const Config = HeadersConfig.TokenHeaders();
        this.QueryUri = `?StoreNo=${this.StoreNo}&SearchId=${this.SearchId}&SearchName=${this.SearchName}&SearchTel=${this.SearchTel}&SearchJTitle=${this.SearchJTitle}&SearchStatus=${this.SearchStatus}&SearchSDate=${this.SearchSDate}&SearchEDate=${this.SearchEDate}&OrderSort=${this.OrderSort}&OrderAD=${this.OrderAD}&PageIndex=${this.PageIndex}&PageAmount=${this.PageAmount}`;
        http.get(`/${this.MyController}/List${this.QueryUri}`, Config).then(response => {
            //console.log(response.data);
            this.MyPaginatedData = response.data;
            //this.MyPaginatedData.Result = response.data.Result;
            //console.log(this.MyPaginatedData);// 會出現 proxy 代理包裝;但可忽視其包裝 直接使用
            //console.log(this.MyPaginatedData);
            this.PageIndex = this.MyPaginatedData.PageIndex;    // 當前頁
            this.PageTotalA = this.MyPaginatedData.TotalCount;  // 總數量
            this.PageTotalP = this.MyPaginatedData.TotalPages;  // 總頁數
            this.Pageli = [];
            //this.MasterMenuPaginatedList.TotalPages;
            // 計算分頁最多顯示五筆
            if (this.PageTotalP > 0) {
                if (this.PageTotalP <= 5) {
                    this.PageFirstli = 1;
                    this.PageLastli = this.PageTotalP;
                } else {
                    // 當前頁面還在第三筆之前 顯示 1-5頁
                    if (this.PageIndex <= 3) {
                        this.PageFirstli = 1;
                        this.PageLastli = 5;
                    } else {
                        if (this.PageIndex + 2 <= this.PageTotalP) {
                            this.PageFirstli = this.PageIndex - 2;
                            this.PageLastli = this.PageIndex + 2;
                        } else if (this.PageIndex + 1 <= this.PageTotalP) {
                            this.PageFirstli = this.PageIndex - 3;
                            this.PageLastli = this.PageIndex + 1;
                        } else if (this.PageIndex == this.PageTotalP) {
                            this.PageFirstli = this.PageIndex - 4;
                            this.PageLastli = this.PageIndex;
                        }
                    }
                }
            }
            //--分頁數值計算,放入 Pageli 中
            for (let li = this.PageFirstli; li <= this.PageLastli; li++) {
                if (this.PageTotalP != 1) {
                    this.Pageli.push(li);
                }
            }
        }).catch(() => {
            alert(`列表資料取得失敗:${this.MyController}`);
        });
    }
    //--列表
    /*ListAllData() {
        const Config = HeadersConfig.TokenHeaders();
        //this.QueryUri = `?PageIndex=${this.PageIndex}&PageAmount=${this.PageAmount}&SearchKey=${this.SearchKey}&SearchContent=${(this.SearchContent)}&OrderSort=${this.OrderSort}&OrderAD=${this.OrderAD}`;
        this.QueryUri = `?StoreNo=${this.StoreNo}&SearchId=${this.SearchId}&SearchName=${this.SearchName}&SearchTel=${this.SearchTel}&SearchJTitle=${this.SearchJTitle}&SearchStatus=${this.SearchStatus}&SearchSDate=${this.SearchSDate}&SearchEDate=${this.SearchEDate}`;
        console.log(this.QueryUri);
        http.get(`/${this.MyController}/ListAll${this.QueryUri}`, Config).then(response => {
            this.MyDataResult = response.data;
        }).catch(e => {
            console.log(`讀取失敗:${e.Message}`);
        });
    }*/
    // 請情
    DetailData(No) {
        const Config = HeadersConfig.TokenHeaders();
        return http.get(`/${this.MyController}/Detail/${No}?StoreNo=${this.StoreNo}`, Config);
    }
    // 新增
    AddData() {
        //console.log(this.MyFormData);
        const Config = HeadersConfig.TokenHeaders();
        return http.post(`/${this.MyController}/Create?StoreNo=${this.StoreNo}`, this.MyFormData, Config);
    }
    // 編輯
    ModifyData() {
        const Config = HeadersConfig.TokenHeaders();
        return http.put(`/${this.MyController}/Edit?StoreNo=${this.StoreNo}`, this.MyFormData, Config);
    }
    // 判斷帳號是否存在
    AccountExists(account) {
        const Config = HeadersConfig.TokenHeaders();
        return http.get(`/${this.MyController}/AccountExists/${account}?StoreNo=${this.StoreNo}`, Config);
    }
}

export default new UsersService();
