<template>
    <aside id="left-panel" class="left-panel">
        <nav class="navbar navbar-expand-sm navbar-default">
            <div id="main-menu" class="main-menu">
                <ul class="nav navbar-nav" v-if="Service.MyMenuClass.length>0">
                    <li class="menu-item-has-children" v-for="(MenuMain,i) in Service.MyMenuClass" :key="i">
                        <a href="#!" class="ChgNavIt01" :title="MenuMain.BraMenuClassName">
                            <img :src="SrcIcon(MenuMain.BraMenuClassIcon)" :alt="MenuMain.BraMenuClassName">{{MenuMain.BraMenuClassName}}
                        </a>
                        <ul class="sub-menu children none">
                            <li v-for="(SubMain,i) in MenuMain.MenuList" :key="i">
                                <router-link :to="`/${$route.params.branch}${SubMain.BraMenuPath}`" :title="SubMain.BraMenuName" :class="[SubMain.BraMenuPath.split('/')[2]==$route.path.split('/')[3]?'active':'']">
                                    {{SubMain.BraMenuName}}
                                </router-link>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </nav>
    </aside>
</template>

<script>
    import LeftMenuService from "@/Services/LeftMenu.service";

    export default {
        name: "LeftMenu",
        data() {
            return {
                Service: LeftMenuService,  //登入服務
            }
        },
        methods: {
            SrcIcon: function (image) {
                return image ? require("@/assets/img/" + image) : "";
            }
        },
        // 實體與掛載完成
        mounted() {
            this.Service.List(this.$route.params.branch);
        },
    };
</script>