          { key: '', name: '豪華雙人', number: ' 9607', floor: '2F' },
          { key: '', name: '豪華雙人', number: ' 9608', floor: '2F' },
          { key: '', name: '豪華雙人2', number: ' 9609', floor: '2F' },
      ],
      RoomsTypeOptionList: [
        { code: 'a', name: '四人房二大床' },
        { code: 'b', name: '四人房一大+二小床' },
        { code: 'c', name: '四人房四小床' },
        { code: 'd', name: '三人房三中床' },
        { code: 'e', name: '二人房一大床' },
        { code: 'f', name: '二人房二中床' },
      ],
      isModified:false //有資料被修改成有背景底色，用於監控左鍵
    }
  },
  components: {
    VueMultiselect,
  },
  computed:{
    FilteredRoomsNumberList: function() {
      let result = []
      if(!this.Modal.RoomType){
        result = JSON.parse(JSON.stringify(this.RoomsNumberList))
      }else{
        result = this.RoomsNumberList.filter(item=>{
          if(item?.name) return item.name.includes(this.Modal.RoomType)
        })
      }

      if(this.SearchingRoomNumber){
        result = result.filter(item=>item.number.trim() == this.SearchingRoomNumber.trim())
      }
      return result
    },
    isCheckedModal: function() {
      if(!this.Modal['RoomType']) return false
      if(!this.Modal['RoomNumbers'].length) return false
      if(!this.Modal['RoomTypeOptions'].length) return false
      return true
    },
  },
  watch:{
    "Modal.RoomType":function(){
      let RoomType = this.Modal.RoomType
      let sessionRoomType = this.Modal.sessionRoomType
      if(RoomType != sessionRoomType){
        this.Modal.RoomNumbers = []
      }
      this.Modal.sessionRoomType = RoomType
    }
  },
  methods: {
    // 頁數:切換
    PageChange(Page) {
      if (Page > 0) {
        this.PageInfo.PageIndex = Page
        this.PageInfo.PageJump = Page
        this.ListData()
      }
    },
    // 頁數:跳頁
    PageJump() {
      if (this.PageInfo.PageJump > this.PageInfo.PageTotalP) {
        alert('超出最後一頁')
        return false
      }
      this.PageInfo.PageIndex = this.PageInfo.PageJump
      this.ListData()
    },
    ListData() {
      //Search 篩選數據
      let filterData = JSON.parse(JSON.stringify(this.RoomsRoomList))
      if (this.Modal['RoomType']) {
        filterData = filterData.filter(
          (item) => item.type == this.Modal['RoomType'],
        )
      }
      if (this.Modal['RoomNumbers'].length > 0) {
        filterData = filterData.filter((item) => {
          return this.Modal['RoomNumbers'].every((num) =>
            item.numberList.includes(num),
          )
        })
      }
      //設定頁面參數
      this.PageInfo['PageTotalP'] = Math.ceil(
        filterData.length / this.PageInfo['PageAmount'],
      )
      this.PageInfo['Pageli'] = []
      for (let index = 1; index <= this.PageInfo['PageTotalP']; index++) {
        this.PageInfo['Pageli'].push(index)
      }
      //依照頁面參數篩選
      let startIndex =
        (this.PageInfo['PageIndex'] - 1) * this.PageInfo['PageAmount']
      let endIndex = this.PageInfo['PageIndex'] * this.PageInfo['PageAmount']
      this.PageInfo['Result'] = filterData.slice(startIndex, endIndex)
    },
    ResetModal() {
      this.Modal['RoomId'] = ''
      this.Modal['RoomType'] = ''
      this.Modal['RoomNumbers'] = []
      this.Modal['RoomTypeOptions'] = []
    },
    //檢查資料
    checkModal(){
      if(!this.Modal['RoomType']) return '請選擇房型'
      if(!this.Modal['RoomNumbers'].length) return '請選擇房號'
      if(!this.Modal['RoomTypeOptions'].length) return '請選擇可支援房型'
      return ''
    },
    // 新增、修改、搜尋:關閉
    AllClose() {
      this.OpenKey = ''
      this.ResetModal()
      this.CancelSearchingRoomNumber()
    },
    // 搜尋:開啟
    SearchOpen() {
      //初始化
      this.OpenKey = 'Search'
      this.ResetModal()
    },
    // 搜尋:查詢
    SearchSubmit() {
      this.ListData()
      this.OpenKey = ''
      this.CancelSearchingRoomNumber()
    },
    // 新增:開啟
    AddOpen() {
      if (this.Auth.MyBraUsersPower.BraPA != 1) {
        alert('沒有新增權限')
        return false
      }
      //初始化
      this.OpenKey = 'Add'
      this.ResetModal()
    },
    // 新增:送出
    AddSubmit() {
      if (this.Auth.MyBraUsersPower.BraPA != 1) {
        alert('沒有新增權限')
        return false
      }
      let checkResult = this.checkModal()
      if(checkResult) return alert(checkResult)

      this.Modal['RoomNumbers'].map(num=>{
        let obj = {
          roomId: new Date().getTime() + Math.floor(Math.random() * 1000),
          type: this.Modal['RoomType'],
          number: num,
          mainOption: this.Modal['RoomMainOption'],
          otherOptions: this.Modal['RoomTypeOptions'].map((item) => item?.name),
          isModified: true, //如果有被修改，則變成true，底色就會變成黃色
          numberList: [num],
          optionList: [...this.Modal['RoomTypeOptions']],
        }
        this.RoomsRoomList.unshift(obj)
      })
      
      this.AllClose()
      this.PageChange(1)
      this.isModified = true
    },
    // 編輯:開啟
    ModifyOpen(index) {
      if (this.Auth.MyBraUsersPower.BraPM != 1) {
        alert('沒有編輯權限')
        return false
      }
      //初始化
      this.OpenKey = 'Modify'
      this.ResetModal()
      let item = this.PageInfo['Result'][index]
      this.Modal['RoomId'] = index
      this.Modal['RoomType'] = item.type
      this.Modal['sessionRoomType'] =item.type
      this.Modal['RoomNumbers'] = [...item.numberList]
      this.Modal['RoomTypeOptions'] = [...item.optionList]
    },
    // 編輯:送出
    ModifySubmit() {
      if (this.Auth.MyBraUsersPower.BraPM != 1) {
        alert('沒有編輯權限')
        return false
      }
      let checkResult = this.checkModal()
      if(checkResult) return alert(checkResult)

      let index = Number(this.Modal.RoomId)
      let item = JSON.parse(JSON.stringify(this.PageInfo['Result'][index]))

      item.type = this.Modal['RoomType']
      item.numberList = [...this.Modal['RoomNumbers']]
      item.number = this.Modal['RoomNumbers'].join(',')
      item.isModified = true
      item.optionList = [...this.Modal['RoomTypeOptions']]
      item.mainOption = this.Modal['RoomMainOption']
      item.otherOptions = this.Modal['RoomTypeOptions'].map((item) => item?.name)

      let roomId = item.roomId
      let theIndex = this.RoomsRoomList.findIndex(
        (obj) => obj?.roomId == roomId,
      )
      this.RoomsRoomList.splice(theIndex, 1)
      this.RoomsRoomList.unshift(item)
      this.AllClose()
      this.PageChange(1)
      this.isModified = true
    },
    // 刪除:開啟
    DeleteOpen(index) {
      if (this.Auth.MyBraUsersPower.BraPD != 1) {
        alert('沒有刪除權限')
        return false
      }
      //初始化
      this.ResetModal()
      let item = this.RoomsRoomList[index]
      this.Modal['RoomId'] = index
      this.Modal['RoomNumbers'] = [...item['numberList']]
    },
    // 刪除:送出
    DeleteSubmit() {
      if (this.Auth.MyBraUsersPower.BraPD != 1) {
        alert('沒有刪除權限')
        return false
      }
      let index = Number(this.Modal['RoomId'])
      this.RoomsRoomList.splice(index, 1)
      this.ResetModal()
      this.ListData()
    },
    // 按下表單
    ClickList(index) {
      let ResultData = this.PageInfo.Result[index]
      ResultData.isModified = false
      let roomId = ResultData.roomId
      let result = this.RoomsRoomList.filter((item) => item?.roomId == roomId)
      if (result[0]) {
        result[0].isModified = false
      }
    },
    // 表單背景顏色
    getResultStyle(bool){
      let obj = {
        backgroundColor: bool ? '#f1e9b3' : '',
        color: bool ? '#3d3d3d' : ''
      }
      return obj
    },
    //選擇房型:選擇li
    SelectRoomType(Item) {
      if (this.Modal.RoomType == Item) {
        return (this.Modal.RoomType = '') //如果按下的是原房型，則取消選擇
      }
      this.Modal.RoomType = Item
    },
    //選擇房型:取消
    CancelRoomType() {
      this.Modal.RoomType = ''
      this.CancelSearchingRoomNumber()
    },
    //選擇房號:選擇li
    SelectRoomNumbers(Item) {
      let number = Item?.number
      let SelectedRoomNumberIndex = this.Modal.RoomNumbers.findIndex(
        (str) => str == number,
      )
      //判斷按下的是否是原房號
      if (SelectedRoomNumberIndex < 0) {
        //不是，就push
        this.Modal.RoomNumbers.push(number)
      } else {
        //是，就移除
        this.Modal.RoomNumbers.splice(SelectedRoomNumberIndex, 1)
      }
    },
    //選擇房號:li選單背景顏色的判別
    FindedRoomNumber(Number) {
      //被選擇的 room number li ，true 背景會變色， false 背景是白色
      if (!Number) return false
      let SelectedRoomNumber = this.Modal.RoomNumbers.find(
        (str) => str == Number,
      )
      if (!SelectedRoomNumber) return false
      return true
    },
    // 選擇房號:[搜尋房號]
    SearchRoomNumber() {
      this.SearchingRoomNumber = this.sessionSearchingRoomNumber
    },
    //選擇房號:取消[搜尋房號]
    CancelSearchingRoomNumber() {
      this.sessionSearchingRoomNumber = ''
      this.SearchRoomNumber()
    },
    //選擇房號:取消
    CancelRoomNumbers() {
      this.Modal.RoomNumbers = []
      this.CancelSearchingRoomNumber()
    },
    //選擇房號:確定
    ChangeRoomModal2ConfirmBtn() {
      //搜尋時，選擇完房型跟房號，按下這個按鈕時，就送出request
      if (this.OpenKey == 'Search') {
        this.SearchSubmit()
      }
    },
    //可支援房型列表
    isSelected(option) {
      return this.Modal.RoomTypeOptions.find(
        (selected) => selected.code === option.code,
      )
    },
    //監控:滑鼠左鍵
    handleMouseDown(event) {
      // event.button === 0 表示左键被按下
      if (event.button === 0 && this.isModified) {
        this.RoomsRoomList.forEach(element => {
          element.isModified = false
        });
        this.ListData()
        this.isModified = false
      }
    }
  },
  // 物件搭建好後執行
  mounted() {
    // 檢視權限
    if (this.Auth.MyBraUsersPower.BraPV != 1) {
      alert('沒有:檢視權限')
      this.$router.push({ path: '/', replace: true })
    }

    // 取得列表資料
    this.ListData()
    
    document.addEventListener('mousedown', this.handleMouseDown)
  },
  beforeDestroy(){
    document.removeEventListener('mousedown', this.handleMouseDown);
  }
}
</script>
