$(function (){
    /*========================================== 
              【 menu open to left 】
    ==========================================*/
    // Menu Trigger
    $(document).on("click", "#header #menuToggle", function (){
        $(this).toggleClass('open');
        if($(this).hasClass("open")){
            if ($('#left-panel .main-menu>ul>li.active>a').length > 0){
                $('#left-panel .main-menu>ul>li.active>a').next().slideUp(function(){
                    $('body').addClass('open');
                    $(this).parent().removeClass('active');
                    if ($('#left-panel ul.sub-menu>li>a.active').length > 0){
                        $('#left-panel ul.sub-menu>li>a.active').parents('li.menu-item-has-children').addClass('active');
                    }
                });
            }else{
                $('body').addClass('open');
                $('#left-panel .main-menu>ul>li>a').next().slideUp(0);
            }
        }else{
            $('body').removeClass('open');
            $('#left-panel .main-menu>ul>li.active>a').next().slideDown();
        }
    });
    //第一層
    $(document).on("click", "#left-panel .main-menu>ul>li>a", function (){
        if($('body').hasClass("open")){
            $('body').removeClass('open');
            $('#header #menuToggle').removeClass('open');
        }

        if ($(this).attr("title")!=$('#left-panel .main-menu>ul>li.active>a').attr("title")){
            $('#left-panel .main-menu>ul>li.active>a').next().slideUp();
            $('#left-panel .main-menu>ul>li.active>a').parent().removeClass('active');

            $(this).next().slideDown();
            $(this).parent().addClass('active');
        }else{
            $('#left-panel .main-menu>ul>li.active>a').next().slideDown();
        }
    });
    /*========================================== 
                【 展開縮合 】
    ==========================================*/
    $(document).on("click", ".cardopen", function (){
        if ($(this).children(".open").attr('class') == 'open') {
            $(this).children(".open").attr('class', 'open none');
        } else {
            $(this).children(".open").attr('class', 'open');
        };
        if ($(this).children(".closed").attr('class') == 'closed none') {
            $(this).children(".closed").attr('class', 'closed');
        } else {

            $(this).children(".closed").attr('class', 'closed none');
        };
    });
    /*========================================== 
                【 展開縮合2 】
    ==========================================*/
    $(document).on("click", ".cardopen2", function (){
        if ($(this).children(".open").attr('class') == 'open') {
            $(this).children(".open").attr('class', 'open none');
        } else {
            $(this).children(".open").attr('class', 'open');
        };
        if ($(this).children(".closed").attr('class') == 'closed none') {
            $(this).children(".closed").attr('class', 'closed');
        } else {
            $(this).children(".closed").attr('class', 'closed none');
        };
    });
    /*========================================== 
                【 訊息顯示/自動隱藏 】
    ==========================================*/
    $(document).on("click", "#open", function (){
        $("#toast").addClass("active");
        setTimeout(() => {
            $("#toast").removeClass("active");
        }, 5000)
    });
    $(document).on("click", "#close", function (){
        $("#toast").removeClass("active");
    });
});

/*========================================== 
            ▲：每頁TOP的／日期、時間
==========================================*/
//--倒數日期
window.setInterval(function () {
      var TTime = new Date();
      var D = TTime.getDate();
      var M = TTime.getMonth() + 1;
      var Y = TTime.getFullYear();
      var hh = TTime.getHours();
      var mm = TTime.getMinutes();
      var ss = TTime.getSeconds();

      var dateForm = Y + "-" + (M <= 9 ? '0' + M : M) + "-" + (D <= 9 ? '0' + D : D);
      var TimeForm = hh + ":" + (mm <= 9 ? '0' + mm : mm) + ":" + ss;
      //--日期
      $(".time_set").html(dateForm);
      //時間
      $(".time_set2").html(TimeForm);
}, 1000);

/*========================================== 
            警示彈跳視窗 BeAlert 
==========================================*/
if (typeof $ === 'function') {
    $(function () {
        var BeAlert = {
            defaultConfig: {
                width: 320,
                height: 170,
                timer: 0,
                type: 'warning',
                showConfirmButton: true,
                showCancelButton: false,
                confirmButtonText: '確定',
                cancelButtonText: '取消'
            },
            html: '<div class="BeAlert_box">' +
                '<div class="BeAlert_image"></div>' +
                '<div class="BeAlert_title"></div>' +
                '<div class="BeAlert_message"></div>' +
                '<div class="BeAlert_button">' +
                '<button class="BeAlert_cancel"></button>' +
                '<button class="BeAlert_confirm"></button>' +
                '</div>' +
                '</div>',
            
            overlay: '<div class="BeAlert_overlay"><div class="modal-backdrop fade show"></div></div>',
            open: function (title, message, callback, o) {
                var opts = {}, that = this;
                $.extend(opts, that.defaultConfig, o);
                $('body').append(that.html).append(that.overlay);
                var box = $('.BeAlert_box');
                box.css({
                    'width': opts.width + 'px',
                    'min-height': opts.height + 'px',
                    'margin-left': -(opts.width / 2) + 'px'
                });
                $('.BeAlert_image').addClass(opts.type);
                title && $('.BeAlert_title').html(title).show(),
                message && $('.BeAlert_message').html(message).show();
                var confirmBtn = $('.BeAlert_confirm'), cancelBtn = $('.BeAlert_cancel');
                opts.showConfirmButton && confirmBtn.text(opts.confirmButtonText).show(),
                opts.showCancelButton && cancelBtn.text(opts.cancelButtonText).show();
                $('.BeAlert_overlay').unbind('click').bind('click', function () {
                    that.close();
                });
                confirmBtn.unbind('click').bind('click', function () {
                    that.close();
                    typeof callback === 'function' && callback(true);
                });
                cancelBtn.unbind('click').bind('click', function () {
                    that.close();
                    typeof callback === 'function' && callback(false);
                });
                var h = box.height();
                box.css({
                    'margin-top': -(Math.max(h, opts.height) / 2 + 100) + 'px'
                });
            },
            close: function () {
                $(".BeAlert_overlay,.BeAlert_box").remove();
            }
          };
          window.alert = function (title, message, callback, opts) {
              BeAlert.open(title, message, callback, opts);
          };
          var _confirm = window.confirm;
          window.confirm = function (title, message, callback, opts) {
              opts = $.extend({type: 'warning', showCancelButton: true}, opts);
              if (typeof callback === 'function') {
                  BeAlert.open(title, message, callback, opts);
              } else {
                  return _confirm(title);
              }
        }
    });
}