    </template>
    <script>
    import AuthService from "@/Services/Auth.service";
    import jquery from 'jquery';
        export default {
            name: "StoreRoomStateList",
            data(){
              return {
                Auth: AuthService,  // 驗證服務
                nowRoom:null,       // 編輯時，當下的資料
                nowRoomIndex:null,  // 編輯時，當下的 index
                RoomDeleteTitle:"", // 刪除時房名
                colorPickerBox:null,// 色盤物件
                resetRoomList:null, // clone資料預設值
                RoomList:[          // 房型資料
                {
                    id:'room_0',
                    status:1,
                    descript:"說明說明",
                    title:'已預訂',
                    colorText:'#FFFFFF',
                    colorTextBg:'#23aaff',
                    colorBg:'#b2e1ff'
                  },
                  {
                    id:'room_1',
                    status:1,
                    descript:"說明說明",
                    title:'待入住',
                    colorText:'#FFFFFF',
                    colorTextBg:'#5278e1',
                    colorBg:'#afc4ff'
                  },
                  {
                    id:'room_2',
                    status:1,
                    descript:"說明說明",
                    title:'已入住',
                    colorText:'#FFFFFF',
                    colorTextBg:'#b38edb',
                    colorBg:'#e7d1ff'
                  },
                  {
                    id:'room_3',
                    status:1,
                    descript:"說明說明",
                    title:'待退房',
                    colorText:'#FFFFFF',
                    colorTextBg:'#11743b',
                    colorBg:'#80dba7'
                  },
                  {
                    id:'room_4',
                    status:1,
                    descript:"說明說明",
                    title:'已退房',
                    colorText:'#FFFFFF',
                    colorTextBg:'#5e6c19',
                    colorBg:'#bfc894'
                  },
                  {
                    id:'room_5',
                    status:1,
                    descript:"說明說明",
                    title:'待打掃',
                    colorText:'#FFFFFF',
                    colorTextBg:'#608078',
                    colorBg:'#c1d8d2'
                  },
                  {
                    id:'room_6',
                    status:1,
                    descript:"說明說明",
                    title:'延長入住',
                    colorText:'#FFFFFF',
                    colorTextBg:'#814c4c',
                    colorBg:'#d7a3a3'
                  },
                  {
                    id:'room_7',
                    status:1,
                    descript:"說明說明",
                    title:'保留房',
                    colorText:'#FFFFFF',
                    colorTextBg:'#298774',
                    colorBg:'#b4ffe5'
                  },
                ]
              };
            },
            methods: {
              //rgb(0,0,0) 轉 #000 功能
              rgb2hex:function (rgb) {
                rgb = rgb.match(/^rgba?[\s+]?\([\s+]?(\d+)[\s+]?,[\s+]?(\d+)[\s+]?,[\s+]?(\d+)[\s+]?/i);
                return (rgb && rgb.length === 4) ? "#" +
                  ("0" + parseInt(rgb[1],10).toString(16)).slice(-2) +
                  ("0" + parseInt(rgb[2],10).toString(16)).slice(-2) +
                  ("0" + parseInt(rgb[3],10).toString(16)).slice(-2) : '';
              },
              //色盤:開啟
              ColorOpen:function(colorEl,color){
                    var winW = $(window).width();
                    colorEl = $(colorEl);
                    var of= colorEl.offset();
                    this.colorPickerBox.bindElem=colorEl[0];
                    this.colorPickerBox.setColorByInput(color);
                    this.colorPickerBox.show();
                    var colorpanel = $(">div:last-child",colorPickerBox.elem_wrap);
    
                    this.colorPickerBox.pancelTop=of.top+colorEl.height()+10;
                    var px = of.left-(colorpanel.width()-colorEl.width())*0.5;
                    if((px+colorpanel.width())>winW)px=winW-colorpanel.width();
                    if(px<0)px=0;
                    this.colorPickerBox.pancelLeft=px;
                    this.colorPickerBox.elem_wrap.style.top=this.colorPickerBox.pancelTop+"px";
                    this.colorPickerBox.elem_wrap.style.left=this.colorPickerBox.pancelLeft+"px";
                    this.colorPickerBox.pancelLeft+= this.colorPickerBox.elem_colorPalette.clientWidth;
                    //console.log(this.colorPickerBox.elem_colorPalette.clientWidth);
                    //this.colorPickerBox.setPosition(0,0);
    //
              },
              // 編輯:開啟
              ModifyOpen: function(e,index,item) {
                if (this.Auth.MyBraUsersPower.BraPM != 1) {
                    alert("沒有編輯權限");
                    return false;
                }
                var el = $("#"+item.id);
                this.nowRoom=item;
                this.nowRoomIndex=index;
                var colorTextBgEL=$(".colorTextBg",el);
                var colorBgEL=$(".colorBg",el);
                var ColorOpen = this.ColorOpen;
                var changeStateModal = $(index==0?"#changeStateDefault":"#changeStateEdit").on('shown.bs.modal', function (e) {
                    change_colorTextBgEL[0].onclick=function(event){
                      ColorOpen(change_colorTextBgEL[0],item.colorTextBg);
                    };
                    change_colorBgEL[0].onclick=function(event){
                      ColorOpen(change_colorBgEL[0],item.colorBg);
                    };
                    
                  }).modal();
                  var change_colorTextBgEL = $(".colorTextBg",changeStateModal).css("backgroundColor",colorTextBgEL.css("backgroundColor"));
                  var change_colorBgEL = $(".colorBg",changeStateModal).css("backgroundColor",colorBgEL.css("backgroundColor"));
                  $("input[name=title]",changeStateModal).val(item.title);
                  $("input[name=status]",changeStateModal).each(function(ci,ce){
                      ce.checked=ce.value==item.status?true:false;
                    });
                  $("textarea[name=descript]",changeStateModal).val(item.descript);
               },
              // 編輯:送出
              ModifySubmit: function(e){
                //console.log([this.nowRoom,this.nowRoomIndex]);
                //console.log(e.currentTarget);
                var changeStateModal = $(e.currentTarget).closest("div.modal");
                var change_colorTextBg = $(".colorTextBg",changeStateModal).css("backgroundColor");
                var change_colorBg = $(".colorBg",changeStateModal).css("backgroundColor");
                //console.log([this.rgb2hex(change_colorTextBg),this.rgb2hex(change_colorBg)]);
                var edititem = this.RoomList[this.nowRoomIndex];
                edititem.colorTextBg=this.rgb2hex(change_colorTextBg);
                edititem.colorBg=this.rgb2hex(change_colorBg);
                edititem.title=$("input[name=title]",changeStateModal).val();
                if(this.nowRoomIndex>0){
                  //editable 
                  edititem.status=$("input[name=status]:checked",changeStateModal).val();
                  edititem.descript=$("textarea[name=descript]",changeStateModal).val();
                }
                $(changeStateModal).modal("hide");
              },
              // 刪除:開啟
              DeleteOpen:function(e,index,item){
                  if (this.Auth.MyBraUsersPower.BraPD != 1) {
                      alert("沒有刪除權限");
                      return false;
                  }
                  var el = $("#"+item.id);
                  this.RoomDeleteTitle= item.title;
                  var _this = this;
                  // 刪除:送出
                  function DeleteSubmit(){
                    _this.RoomList.splice(index,1);
                    deleteAlertModal.modal("hide");
                  }
                  var deleteAlertModal = $("#deleteAlert").on('hide.bs.modal', function (e) {
                    RoomDelete.off("click",DeleteSubmit);
                  }).modal();
                  var RoomDelete = $("button.RoomDelete",deleteAlertModal);
                  RoomDelete.on("click",DeleteSubmit);
               },
               // 新增:開啟
               AddOpen:function(){
                  if (this.Auth.MyBraUsersPower.BraPA != 1) {
                      alert("沒有新增權限");
                      return false;
                  }
                  var ColorOpen = this.ColorOpen;
                  var colorTextBg=this.RoomList[0].colorTextBg;
                  var colorBg=this.RoomList[0].colorBg;
                  function onshown(e){
                    $("input[name=title]",addStateModal).val("");
                    $("textarea[name=descript]",addStateModal).val("");
                    $("input[name=status_add]",addStateModal)[0].checked=true;
                    add_colorTextBgEL[0].onclick=function(event){
                      ColorOpen(add_colorTextBgEL[0],colorTextBg);
                    };
                    add_colorBgEL[0].onclick=function(event){
                      ColorOpen(add_colorBgEL[0],colorBg);
                    };
                    addStateModal.off('shown.bs.modal',onshown); 
                  }
                  var addStateModal = $("#addState").on('shown.bs.modal',onshown).modal();
                  var add_colorTextBgEL = $(".colorTextBg",addStateModal).css("backgroundColor",colorTextBg);
                  var add_colorBgEL = $(".colorBg",addStateModal).css("backgroundColor",colorBg);
               },
               // 新增:送出
               AddSubmit:function(event){
                  var addStateModal = $("#addState");
                  var inputTitle;
                  var title=$.trim((inputTitle=$("input[name=title]",addStateModal)).val()),
                  status=$("input[name=status_add]:checked",addStateModal).val(),
                  colorTextBg=this.rgb2hex($(".colorTextBg",addStateModal).css("backgroundColor")),
                  colorBg=this.rgb2hex($(".colorBg",addStateModal).css("backgroundColor")),
                  descript=$("textarea[name=descript]",addStateModal).val();
                  var dub=false;
                  this.RoomList.forEach(function(o,i){
                    if(dub)return;
                    if(o.title==title)dub=true;
                  });
                  function inputfocus(){
                    inputTitle.addClass("warning")[0].focus();
                  }
                  if(title==""){
                    inputfocus();
                    return;
                  }
                  if(dub){
                    var nar=$("#NameRepeatAlert").on('hidden.bs.modal',onmodalhide).modal();
                    function onmodalhide(){
                      inputfocus();
                      nar.off("hidden.bs.modal",onmodalhide);
                    }
                    return;
                  }else{
                    inputTitle.removeClass("warning");
                  }
                  //new id
                  var id = 'room_'+(new Date).getTime();
                  var item = {
                    id:id,
                    title:title,
                    status:status,
                    descript:descript,
                    colorTextBg:colorTextBg,
                    colorBg:colorBg
                  };
                  this.RoomList.splice(1,0,item);
                  addStateModal.modal("hide");
               },
               // 恢復預設:送出
               RestoreSubmit:function(){
                this.RoomList = JSON.parse(JSON.stringify(this.resetRoomList));
                $("#restoreAlert").modal("hide");
    
               }
    
            },
            mounted() {
                // 檢視權限
                if (this.Auth.MyBraUsersPower.BraPV != 1) {
                    alert("沒有:檢視權限");
                    this.$router.push({ path: "/", replace: true });
                }
                
                //clone預設值
                this.resetRoomList = JSON.parse(JSON.stringify(this.RoomList));
                /* console.log(this); */
                //建立色盤
                window.colorPickerBox=
                this.colorPickerBox = Colorpicker.create({
                    el: "colorPickerBox",
                    class:"pmscolorpicker",
                    color: "#5278e1",
                    change: function(elem, hex) {
                        elem.style.backgroundColor = hex;
                    }
                });
                //畫面resize時關閉色盤
                $(window).on("resize",function(){
                  colorPickerBox.hide();
                });
               
            }
        };
    
    
    </script>