import http from "@/Services/HttpAxios";
import HeadersConfig from "@/Services/HeadersConfig";                               // 取得驗證Header
import { BraUsersAuthLogin, BraUsersAuthStore, BraUsersAuthMenu, BraUserLogin } from "@/Models/BraUsers/BraUserAuth.model";// 驗證選單回傳權限資訊
import { BraUsersPower } from "@/Models/BraUsers/BraUsersPower.model";

class AuthService {
    MyBraUserLogin = new BraUserLogin();            // 登入資ㄌ要存取
    MyBraUserAuthLogin = new BraUsersAuthLogin();   // 使用者驗證回應接收
    MyBraUsersAuthStore = new BraUsersAuthStore();  // 商店驗證回傳資料
    MyBraUsersAuthMenu = new BraUsersAuthMenu();    // 使用者選單項目權限回傳資訊
    MyBraUsersPower = new BraUsersPower();          // 目前項目權限
    CurrentItemCode = "";                           // 用於存放當前的ItemCode
    CurrentItemId = "";                             // 用於存放當前的ItemId
    CurrentSotreId = "";                            // 目前選擇的商店
    
    // 登入
    LoginService() {
        return http.post(`/OAuth/LoginCheck`, this.MyBraUserLogin);
    }
    // 選擇商店
    StoreCheck(StoreNo) {
        const Config = HeadersConfig.LoginTokenHeaders();// 取得登入權杖
        return http.post(`/OAuth/StoreCheck?StoreNo=${StoreNo}`, null, Config);
    }
    // 根據目前點選功能進行權限驗證回傳該使用者使用權限
    async AuthCheck(StoreNo,MenuCode) {
        return new Promise((resolve, reject) => {
            const Config = HeadersConfig.TokenHeaders();// 取得登入權杖
            //console.log(`/OAuth/MenuCheck?StoreNo=${StoreNo}&MenuCode=${MenuCode}`);
            try {
                http.post(`/OAuth/MenuCheck?StoreNo=${StoreNo}&MenuCode=${MenuCode}`, null, Config).then(response => {
                    //console.log(response.data);
                    // 對應使用的項目以及權限 Model
                    // 其他頁面服務只需要 import AuthService from "../Services/AuthService"; 便可以取得目前 PowerList 的權限狀態

                    this.MyBraUsersAuthMenu = response.data;
                    console.log(this.MyBraUsersAuthMenu);
                    if (this.MyBraUsersAuthMenu.Status == "SUCCESS") {
                        // 覆蓋功能項目使用者權限
                        this.MyBraUsersPower = Object.assign({}, this.MyBraUsersAuthMenu.PowerList[0]);
                        resolve(true);
                    } else {
                        reject(false);
                    }
                }).catch(() => {
                    reject(false);
                });
            } catch {
                reject(false);
            }
        });
    }
}

export default new AuthService();