import { StatusMessage } from "@/Models/StatusMessage";

// 登入帳號驗證
class BraUsersAuthLogin extends StatusMessage {
    constructor(StatusMessage) {
        super(StatusMessage);
        this.LoginToken = "";
        this.StoreList = [];
    }
}
// 登入分店驗證
class BraUsersAuthStore extends StatusMessage {
    constructor() {
        super(); // 使用父類別的 constructor
        // 商店令牌
        this.StoreToken = '';
        // 商店編號
        this.BraStoreNo = '';
        // 商店名稱
        this.BraStoreName = '';
    }
}
// 選單驗證
class BraUsersAuthMenu extends StatusMessage {
    constructor() {
        super(); // 使用父類別的 constructor
        // 商店名稱
        this.BraStoreName = '';
        // 使用者編號
        this.BraUserNo = '';
        // 使用者名稱
        this.BraUserName = '';
        // 使用者總權限
        this.BraUserAllPower = 0;
        // 權限列表
        this.PowerList = [];
    }
}
// 登入表單
class BraUserLogin {
    constructor() {
        this.BraInputAccount = "";
        this.BraInputPassword = "";
    }
}

// 在需要的地方導入這些 class
export { BraUsersAuthLogin, BraUsersAuthStore, BraUsersAuthMenu, BraUserLogin };