import http from "./HttpAxios";
import HeadersConfig from "./HeadersConfig";                                        // 取得驗證Header
import { BraDataList } from "@/Models/BraSysData/BraSysData.model";                 // 共用資料Model
import { BraOrdersForm, BraOrdersProjectsPlan } from "@/Models/BraOrders/BraOrders.model";  // 訂單資料Model

class OrdersStayService {
    MyController = "BraOrdersStay";
    MyFormData = new BraOrdersForm();               // 表單資料
    MyStoreChannelData = new BraDataList();         // 通路來源資料
    MyStoreAddBuyData = new BraDataList();          // 加購項目資料
    MyStoreTagData = new BraDataList();             // 標籤資料
    MyRoomTypeData = new BraDataList();             // 房型資料

    QueryUri = "";

    // 新增
    AddData() {
        //console.log(this.MyFormData);
        const Config = HeadersConfig.TokenHeaders();
        return http.post(`/${this.MyController}/Create?StoreNo=${this.StoreNo}`, this.MyFormData, Config);
    }
    // 促銷專案方案資料
    ProjectsPlanData(StartDate, EndDate, RoomType) {
        const Config = HeadersConfig.TokenHeaders();
        this.QueryUri = `?StoreNo=${this.StoreNo}&SDate=${StartDate}&EDate=${EndDate}&RTNo=${RoomType}`;
        //console.log(this.QueryUri);
        return http.get(`/${this.MyController}/ProjectsPlanData${this.QueryUri}`, Config);
    }
    // 通路來源資料
    StoreChannelData() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/StoreChannelData?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyStoreChannelData = response.data;
            if (response.data.Status == "SUCCESS") {
            } else {
                alert(response.data.Message);
            }
        }).catch(() => {
            alert(`通路來源資料取得失敗:StoreChannelData`);
        });
    }
    // 加購資料
    StoreAddBuyData() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/StoreAddBuyData?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyStoreAddBuyData = response.data;
            if (response.data.Status == "SUCCESS") {
            } else {
                alert(response.data.Message);
            }
        }).catch(() => {
            alert(`加購資料取得失敗:StoreAddBuyData`);
        });
    }
    // 標籤資料
    StoreTagData() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/StoreTagData?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyStoreTagData = response.data;
            if (response.data.Status == "SUCCESS") {
            } else {
                alert(response.data.Message);
            }
        }).catch(() => {
            alert(`標籤資料取得失敗:StoreTagData`);
        });
    }
    // 房型資料
    RoomTypeData() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/RoomTypeData?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyRoomTypeData = response.data;
            if (response.data.Status == "SUCCESS") {
            } else {
                alert(response.data.Message);
            }
        }).catch(() => {
            alert(`房型資料取得失敗:RoomTypeData`);
        });
    }
}

export default new OrdersStayService();