                                <th style="min-width:207px; width:237px;"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr class="js_notSear js_hide js_sort1" v-for="(ResultData,i) in Service.MyPaginatedData.Result" :key="i">
                                <td class="bold cDark point">{{ResultData.BraBlacklistName}}</td>
                                <td>{{ResultData.BraBlacklistTel}}</td>
                                <td>{{ResultData.BraBlacklistSexTxt}}</td>
                                <td>{{ResultData.BraBlacklistReason}}</td>
                                <td>{{ResultData.BraBlacklistAddDate}}</td>
                                <td>
                                    <button type="button" title="詳情" class="btn btnSt3 ft1">詳情</button>
                                    <button type="button" title="編輯" class="btn btnSt5 ft1" @click="ModifyOpen(ResultData.BraBlacklistNo)">編輯</button>
                                    <button type="button" title="删除" class="btn btnDel ft1" @click="DeleteSubmit(ResultData.BraBlacklistNo)">删除</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <!-- pagination -->
                <div class="jqPages sty point" v-if="Service.PageTotalP>1">
                    <div class="padDingL bgc hoverDisable" name="whj_previousPage" v-if="Service.PageIndex>1" @click="PageChange(Service.PageIndex-1);">❮ 往前</div>
                    <div class="borDer padDing bgc" name="whj_firstPage" v-if="Service.PageIndex>1" @click="PageChange(1);">1</div>
                    <div class="maL" v-if="Service.PageIndex>1"><span class="material-icons">more_horiz</span></div>
                    <template v-for="(li,i) in Service.Pageli" :key="i">
                        <div class="borDer padDing bgc checKed" v-if="Service.PageIndex==li" name="whj_page" data-page="li">{{li}}</div>
                        <div class="borDer padDing bgc hoVer" v-if="Service.PageIndex!=li" name="whj_page" data-page="li" @click="PageChange(li)">{{li}}</div>
                    </template>
                    <div class="maR" v-if="Service.PageIndex<Service.PageTotalP"><span class="material-icons">more_horiz</span></div>
                    <div class="borDer padDing bgc hoVer" name="whj_lastPage" v-if="Service.PageIndex<Service.PageTotalP" @click="PageChange(Service.PageTotalP);">{{Service.PageTotalP}}</div>
                    <div class="padDingR bgc hoVer" name="whj_nextPage" v-if="Service.PageIndex<Service.PageTotalP" @click="PageChange(Service.PageIndex+1);">往後 ❯</div>
                    <div class="coLor jumPg">跳頁</div>
                    <input type="text" class="coLor" name="whj_toPage" v-model="Service.PageJump">
                    <div class="borDerRd padDing bgc hover butPoin" name="whj_confirm" @click="PageJump()">確定</div>
                </div>
            </section>
        </div><!-- section out -->
    </article>
    <!--===== Dialog:新增黑名單 =====-->
    <div class="modal fade zId52" id="addBlacklist" tabindex="-1" aria-labelledby="" aria-hidden="true" data-backdrop="static" data-keyboard="false">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content cGy ft2">
                <!-- Tit -->
                <div class="w100 posHead2">
                    <div class="ft5 cDark topTit bgcL2">
                        新增黑名單
                    </div>
                </div>
                <!-- Content -->
                <div class="modalScrY">
                    <div class="modal-body">
                        <!-- modal-body -->
                        <!-- form -->
                        <ul class="group paB15 clear">
                            <li class="flL w33a marRS">
                                <div class="formCon w100">
                                    <input class="ft1 w100" placeholder="姓名" v-model="Service.MyFormData.BraBlacklistName">
                                </div>
                            </li>
                            <li class="flL w33a marRS">
                                <div class="formCon w100">
                                    <input class="ft1 w100" placeholder="電話:+886-9123465 | 0932145668" v-model="Service.MyFormData.BraBlacklistTel">
                                </div>
                            </li>
                            <li class="formSel w33">
                                <select class="formCon" v-model="Service.MyFormData.BraBlacklistSex">
                                    <option value="0">性別</option>
                                    <option v-for="(Data,i) in PMSConfig.MyOptionData.MemberSex" :key="i" :value="Data.Val">{{Data.Txt}}</option>
                                </select>
                            </li>
                        </ul>
                        <!-- form -->
                        <ul class="group paB15 clear">
                            <li class="flL w100">
                                <textarea rows="2" placeholder="列為黑名單原因" class="formCon hAuto w100" v-model="Service.MyFormData.BraBlacklistReason"></textarea>
                            </li>
                        </ul>
                        <!-- form -->
                        <ul class="group paB2 clear">
                            <li class="flL w100 itemC">
                                <span class="cDark ft1 maR1">加入黑名單日期</span>
                                <form class="js_info_birdN" style="width:calc(100% - 126px);">
                                    <input class="formCon nonepad w100" type="date" name="BlacklistAddDate" v-model="Service.MyFormData.BraBlacklistAddDate">
                                </form>
                            </li>
                        </ul>
                    </div><!-- modal-body -->
                </div>
                <!-- Button -->
                <div class="roomModalPos sty">
                    <div class="btnF">
                        <button type="button" class="btn btnSt1 ft1" data-dismiss="modal">
                            取消
                        </button>
                        <button type="button" class="btn btnSure ft1 js_showCustBtn" @click="onCreate()">
                            確定
                        </button>
                    </div>
                </div><!-- button -->
            </div>
        </div>
    </div>
    <!--===== Dialog:編輯黑名單 =====-->
    <div class="modal fade zId52" id="editBlacklist" tabindex="-1" aria-labelledby="" aria-hidden="true" data-backdrop="static" data-keyboard="false">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content cGy ft2">
                <!-- Tit -->
                <div class="w100 posHead2">
                    <div class="ft5 cDark topTit bgcL2">
                        編輯黑名單
                    </div>
                </div>
                <!-- Content -->
                <div class="modalScrY">
                    <div class="modal-body">
                        <!-- modal-body -->
                        <!-- form -->
                        <ul class="group paB15 clear">
                            <li class="flL w33a marRS">
                                <div class="formCon w100">
                                    <input class="ft1 w100" placeholder="姓名" v-model="Service.MyFormData.BraBlacklistName">
                                </div>
                            </li>
                            <li class="flL w33a marRS">
                                <div class="formCon w100">
                                    <input class="ft1 w100" placeholder="電話:+886-9123465 | 0932145668" v-model="Service.MyFormData.BraBlacklistTel" p>
                                </div>
                            </li>
                            <li class="formSel w33">
                                <select class="formCon" v-model="Service.MyFormData.BraBlacklistSex">
                                    <option value="0">性別</option>
                                    <option v-for="(Data,i) in PMSConfig.MyOptionData.MemberSex" :key="i" :value="Data.Val">{{Data.Txt}}</option>
                                </select>
                            </li>
                        </ul>
                        <!-- form -->
                        <ul class="group paB15 clear">
                            <li class="flL w100">
                                <textarea rows="2" placeholder="列為黑名單原因" class="formCon hAuto w100" v-model="Service.MyFormData.BraBlacklistReason"></textarea>
                            </li>
                        </ul>
                        <!-- form -->
                        <ul class="group paB2 clear">
                            <li class="flL w100 itemC">
                                <span class="cDark ft1 maR1">加入黑名單日期</span>
                                <form class="js_info_birdN" style="width:calc(100% - 126px);">
                                    <input class="formCon nonepad w100" type="date" name="BlacklistAddDate" v-model="Service.MyFormData.BraBlacklistAddDate">
                                </form>
                            </li>
                        </ul>
                    </div><!-- modal-body -->
                </div>
                <!-- Button -->
                <div class="roomModalPos sty">
                    <div class="btnF">
                        <button type="button" class="btn btnSt1 ft1" data-dismiss="modal">
                            取消
                        </button>
                        <button type="button" class="btn btnSure ft1" @click="ModifySubmit()">
                            異動
                        </button>
                    </div>
                </div><!-- button -->
            </div>
        </div>
    </div>
</template>
<script>
    import AuthService from "@/Services/Auth.service";
    import SysDataService from "@/Services/SysData.service";
    import PMSKitHelper from "@/Helper/PMSKit.helper.js";
    import StoreBlacklistService from "@/Services/StoreBlacklist.service";
    import ValidatorHelper from "@/Helper/Validator.helper";
    import { BraStoreBlacklistForm } from "@/Models/BraStoreBlacklist/BraStoreBlacklist.model"; //黑名單資料Model
    import moment from "moment";

    export default {
        name: "StoreBlacklistList",
        data() {
            return {
                Auth: AuthService,              // 驗證服務
                PMSConfig: SysDataService,      // PMS系統配置
                PMSKit: PMSKitHelper,           // PMS套件
                Service: StoreBlacklistService, // 模組資料服務
                Validator: ValidatorHelper,     // 驗證工具
                Editable: false,                // 編輯模式
            }
        },
        methods: {
            // 頁數:切換
            PageChange(Page) {
                if (Page > 0) {
                    this.Service.PageIndex = Page;
                    this.Service.PageJump = Page;
                    this.Service.ListData();
                }
            },
            // 頁數:跳頁
            PageJump() {
                if (this.Service.PageJump > this.Service.PageTotalP) {
                    alert("超出最後一頁");
                    return false;
                }
                this.Service.PageIndex = this.Service.PageJump;
                this.Service.ListData();
            },
            // 搜尋:查詢
            SearchSubmit() {
                this.Service.ListData();
            },
            // 搜尋:清除
            SearchReset() {
                this.Service.SearchName = "";   // 姓名
                this.Service.SearchTel = "";    // 電話
                this.Service.SearchSDate = "";  // 建立開始日期
                this.Service.SearchEDate = "";  // 建立結束日期

                $("#SearchBlock #SearchDate").trigger('cancel.daterangepicker');
                $("#SearchBlock #SearchReset").addClass('none');
                this.Service.ListData();
            },
            // 搜尋:變更
            SearchChange() {
                if ($("#SearchBlock #SearchDate").val()!=""){
                    this.Service.SearchSDate = $("#SearchBlock #SearchDate").val().split(' ~ ')[0];
                    this.Service.SearchEDate = $("#SearchBlock #SearchDate").val().split(' ~ ')[1];
                }else{
                    this.Service.SearchSDate = "";
                    this.Service.SearchEDate = "";
                }
                $("#SearchBlock #SearchReset").removeClass('none');
            },
            // 編輯:開啟
            ModifyOpen(No) {
                if (this.Auth.MyBraUsersPower.BraPM != 1) {
                    alert("沒有編輯權限");
                    return false;
                }

                // API串接
                this.Service.DetailData(No).then(response => {
                    this.Service.MyFormData = response.data;
                    if (this.Service.MyFormData.Status == "SUCCESS") {
                        $("#editBlacklist").modal("show");
                    } else {
                        alert("取得資料失敗");
                        return false;
                    }
                }).catch(e => {
                    alert("取得詳細資料失敗!");
                    return false;
                });
            },
            // 編輯:送出
            ModifySubmit() {
                // 編輯權限
                if (this.Auth.MyBraUsersPower.BraPM != 1) {
                    alert("沒有編輯權限");
                    return false;
                }

                let ERRORNo = 0;
                let ERROTxt = "";
                if (this.Service.MyFormData.BraBlacklistName.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入姓名<br>";
                }
                if (this.Service.MyFormData.BraBlacklistTel.trim() == "" || !this.Validator.phone(this.Service.MyFormData.BraBlacklistTel)) {
                    ERRORNo += 1;
                    ERROTxt += "請輸入正確電話<br>";
                }
                if (this.Service.MyFormData.BraBlacklistSex < 1) {
                    ERRORNo += 1;
                    ERROTxt += "請選擇性別<br>";
                }
                if (this.Service.MyFormData.BraBlacklistReason.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入原因<br>";
                }
                if (this.Service.MyFormData.BraBlacklistAddDate.trim() == "") {
                    this.Service.MyFormData.BraBlacklistAddDate = moment().format("YYYY-MM-DD");
                }
                if (ERRORNo > 0) {
                    alert(ERROTxt);
                    return false;
                }
                // API串接
                this.Service.ModifyData().then(response => {
                    this.Service.MyFormData = response.data;
                    if (this.Service.MyFormData.Status == "SUCCESS") {
                        alert("異動成功!");
                        $("#editBlacklist").modal("hide");
                        this.Service.ListData();
                    }
                }).catch(e => {
                    alert("新增失敗!");
                    return false;
                });
            },
            // 刪除:送出
            DeleteSubmit(No) {
                // 刪除權限
                if (this.Auth.MyBraUsersPower.BraPD != 1) {
                    alert("沒有刪除權限");
                    return false;
                }

                if (confirm("確認刪除該筆資料?")) {
                    this.Service.MyFormData = new BraStoreBlacklistForm();
                    this.Service.MyFormData.BraBlacklistNo = No;
                    if (parseInt(this.Service.MyFormData.BraBlacklistNo) > 0) {
                        // API串接
                        this.Service.DeleteData().then(response => {
                            this.Service.ListData();
                        }).catch(e => {
                            alert("刪除失敗!");
                            return false;
                        });
                    } else {
                        alert("錯誤:刪除錯誤資訊");
                        return false;
                    }
                }
            }
        },
        // 實體已建立，狀態與事件已初始化完成
        created() {
            // 取得目前分店編號
            this.PMSConfig.StoreNo = this.$route.params.branch;
            this.Service.StoreNo = this.$route.params.branch;
        },
        // 實體與掛載完成
        mounted() {
            let vthis = this;

            // 檢視權限
            if (this.Auth.MyBraUsersPower.BraPV != 1) {
                alert("沒有:檢視權限");
                this.$router.push({ path: "/", replace: true });
            }

            // 搜尋:日期 > 載入日期套件
            this.PMSKit.DatePicker($("#SearchBlock #SearchDate"), 'Range', 'YYYY-MM-DD');
            $("#SearchBlock #SearchDate").on('apply.daterangepicker', function(ev, picker) {
                vthis.SearchChange();
            });

            // 取得選項資料
            this.PMSConfig.OptionData();
            // 取得列表資料
            this.Service.ListData();
        }
    };
</script>