import http from "./HttpAxios";
import HeadersConfig from "./HeadersConfig";                                        // 取得驗證Header
import { BraDataList } from "@/Models/BraSysData/BraSysData.model";                 // 共用資料Model
import { BraStoreSettings } from "@/Models/BraStore/BraStore.model";                // 商店資料Model

class SysDataService {
    MyController = "BraSysData";
    MyCountryData = new BraDataList();              // 國家資料
    MyCityData = new BraDataList();                 // 縣市資料
    MyTownData = new BraDataList();                 // 鄉鎮資料
    MyOptionData = new BraDataList();               // 選項資料
    MyStoreSettings = new BraStoreSettings();       // 商店設定資料

    StoreNo = "";
    
    // 國家資料
    CountryData(CountryType="") {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/CountryData?StoreNo=${this.StoreNo}&CountryType=${CountryType}`, Config).then(response => {
            //console.log(response.data);
            this.MyCountryData = response.data;
        }).catch(() => {
            alert(`國家資料取得失敗:CountryData`);
        });
    }
    // 縣市資料
    CityData(CountryNo="") {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/CityData?StoreNo=${this.StoreNo}&CountryNo=${CountryNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyCityData = response.data;
        }).catch(() => {
            alert(`縣市資料取得失敗:CityData`);
        });
    }
    // 鄉鎮資料
    TownData(CityNo="") {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/TownData?StoreNo=${this.StoreNo}&CityNo=${CityNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyTownData = response.data;
        }).catch(() => {
            alert(`鄉鎮資料取得失敗:TownData`);
        });
    }
    // 選項資料
    OptionData() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/OptionData?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyOptionData = response.data;
        }).catch(() => {
            alert(`選項資料取得失敗:OptionData`);
        });
    }
    // 商店設定項目
    StoreSettings() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/StoreSettings?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyStoreSettings = response.data;
        }).catch(() => {
            alert(`商店設定項目取得失敗:StoreSettings`);
        });
    }
}

export default new SysDataService();
