import { StatusMessage } from "@/Models/StatusMessage";

// 商店設定項目
class BraStoreSettings extends StatusMessage {
    constructor(StatusMessage) {
        super(StatusMessage);
        // 入住時間
        this.BraStoreCheckinTime = '';
        // 退房時間
        this.BraStoreCheckoutTime = '';
        // 保留時間
        this.BraStoreKeepTime = '';
        // 訂金比例
        this.BraStoreDepositRatio = 0;
        // 訂金期限天數
        this.BraStoreDepositDay = 0;
        // 訂金付款通知
        this.BraStoreDepositNotice = 0;
    }
}

// 在需要的地方導入這些 class
export { BraStoreSettings };