import http from "./HttpAxios";
import HeadersConfig from "./HeadersConfig";                                        // 取得驗證Header
import { DataPaginated } from "@/Models/ResultData.model";                          // 列表共用分頁結果
import { BraDataList } from "@/Models/BraSysData/BraSysData.model";                 // 共用資料Model
import { BraOrdersDetail, BraOrdersForm } from "@/Models/BraOrders/BraOrders.model";// 訂單資料Model

class OrdersService {
    MyController = "BraOrders";
    MyPaginatedData = new DataPaginated();          // 分頁承接資料
    MyDataResult = new BraDataList();               // 非分頁承接資料
    MyDetailData = new BraOrdersDetail();           // 詳細資料
    MyFormData = new BraOrdersForm();               // 表單資料
    MyStoreChannelData = new BraDataList();         // 通路來源資料

    // 搜尋欄位(獨立)
    StoreNo = "";
    SearchName = "";        // 姓名
    SearchTel = "";         // 電話
    SearchState = "";       // 訂單狀態
    SearchDateType = "";    // 日期類型
    SearchDate = "";        // 日期
    SearchSerialNo = "";    // 訂單編號
    SearchChannel = "";     // 通路來源
    // 排序欄位
    OrderSort = 1;          // 根據API排序欄位進行預設
    OrderAD = 0;            // 根據API排序條件設定 0=ASC 1=DESC
    // 分頁參數
    PageIndex = 1;          // 預設第一頁
    PageAmount = 10;        // 每頁10筆
    PageJump = 1;           // 跳頁
    PageTotalA = 0;         // 總數量
    PageTotalP = 0;         // 總頁數
    PageFirstli = 0;        // 計算分頁暫存使用
    PageLastli = 0;         // 計算分頁暫存使用
    Pageli = [];            // 存放頁數
    QueryUri = "";

    // 列表
    ListData() {
        const Config = HeadersConfig.TokenHeaders();
        this.QueryUri = `?StoreNo=${this.StoreNo}&SearchName=${(this.SearchName)}&SearchTel=${this.SearchTel}&SearchState=${this.SearchState}&SearchDateType=${this.SearchDateType}&SearchDate=${this.SearchDate}&SearchSerialNo=${this.SearchSerialNo}&SearchChannel=${this.SearchChannel}&OrderSort=${this.OrderSort}&OrderAD=${this.OrderAD}&PageIndex=${this.PageIndex}&PageAmount=${this.PageAmount}`;
        //console.log(this.QueryUri);
        http.get(`/${this.MyController}/List${this.QueryUri}`, Config).then(response => {
            //console.log(response.data);
            this.MyPaginatedData = response.data;
            //this.MyPaginatedData.Result = response.data.Result;
            //console.log(this.MyPaginatedData);//會出現 proxy 代理包裝;但可忽視其包裝 直接使用
            //console.log(this.MyPaginatedData);
            this.PageIndex = this.MyPaginatedData.PageIndex;    // 當前頁
            this.PageTotalA = this.MyPaginatedData.TotalCount;  // 總數量
            this.PageTotalP = this.MyPaginatedData.TotalPages;  // 總頁數
            this.Pageli = [];
            //this.MasterMenuPaginatedList.TotalPages;
            // 計算分頁最多顯示五筆
            if (this.PageTotalP > 0) {
                if (this.PageTotalP <= 5) {
                    this.PageFirstli = 1;
                    this.PageLastli = this.PageTotalP;
                } else {
                    // 當前頁面還在第三筆之前 顯示 1-5頁
                    if (this.PageIndex <= 3) {
                        this.PageFirstli = 1;
                        this.PageLastli = 5;
                    } else {
                        if (this.PageIndex + 2 <= this.PageTotalP) {
                            this.PageFirstli = this.PageIndex - 2;
                            this.PageLastli = this.PageIndex + 2;
                        } else if (this.PageIndex + 1 <= this.PageTotalP) {
                            this.PageFirstli = this.PageIndex - 3;
                            this.PageLastli = this.PageIndex + 1;
                        } else if (this.PageIndex == this.PageTotalP) {
                            this.PageFirstli = this.PageIndex - 4;
                            this.PageLastli = this.PageIndex;
                        }
                    }
                }
            }
            // 分頁數值計算,放入 Pageli 中
            for (let li = this.PageFirstli; li <= this.PageLastli; li++) {
                if (this.PageTotalP != 1) {
                    this.Pageli.push(li);
                }
            }
        }).catch(() => {
            alert(`列表資料取得失敗:${this.MyController}`);
        });
    }
    // 詳情
    DetailData(No) {
        const Config = HeadersConfig.TokenHeaders();
        return http.get(`/${this.MyController}/Detail/${No}?StoreNo=${this.StoreNo}`, Config);
    }
    // 編輯
    ModifyData() {
        const Config = HeadersConfig.TokenHeaders();
        return http.put(`/${this.MyController}/Edit?StoreNo=${this.StoreNo}`, this.MyFormData, Config);
    }
    // 刪除
    DeleteData() {
        const Config = HeadersConfig.TokenHeaders();
        let FormDataArray = [];
        FormDataArray.push(this.MyFormData);
        Config.data = FormDataArray;
        return http.delete(`/${this.MyController}/Delete?StoreNo=${this.StoreNo}`, Config);
    }
    // 通路來源資料
    StoreChannelData() {
        const Config = HeadersConfig.TokenHeaders();
        http.get(`/${this.MyController}/StoreChannelData?StoreNo=${this.StoreNo}`, Config).then(response => {
            //console.log(response.data);
            this.MyStoreChannelData = response.data;
            if (response.data.Status == "SUCCESS") {
            } else {
                alert(response.data.Message);
            }
        }).catch(() => {
            alert(`通路來源資料取得失敗:BraStoreChannel`);
        });
    }
}

export default new OrdersService();