<script>
    import AuthService from "@/Services/Auth.service";
    import SysDataService from "@/Services/SysData.service";
    import PMSKitHelper from "@/Helper/PMSKit.helper.js";
    import UsersService from "@/Services/Users.service";
    import { BraUsersForm } from "@/Models/BraUsers/BraUsers.model";    //使用者資料Model

    export default {
        name: "UsersList",
        data() {
            return {
                Auth: AuthService,              // 驗證服務
                PMSConfig: SysDataService,      // PMS系統配置
                PMSKit: PMSKitHelper,           // PMS套件
                Service: UsersService,          // 模組資料服務
                Editable: false,                // 編輯模式
                AccountTimeoutId: null,         // 檢查帳號時效
                AccountStatus: false,           // 該帳號是否可使用
            }
        },
        methods: {
            // 頁數:切換
            PageChange(Page) {
                if (Page > 0) {
                    this.Service.PageIndex = Page;
                    this.Service.PageJump = Page;
                    this.Service.ListData();
                }
            },
            // 頁數:跳頁
            PageJump() {
                if (this.Service.PageJump > this.Service.PageTotalP) {
                    alert("超出最後一頁");
                    return false;
                }
                this.Service.PageIndex = this.Service.PageJump;
                this.Service.ListData();
            },
            // 搜尋:查詢
            SearchSubmit() {
                this.Service.ListData();
            },
            // 搜尋:清除
            SearchReset() {
                this.Service.SearchId = "";     // 編號
                this.Service.SearchName = "";   // 姓名
                this.Service.SearchTel = "";    // 電話
                this.Service.SearchJTitle = ""; // 職稱
                this.Service.SearchStatus = ""; // 人員狀態
                this.Service.SearchSDate = "";  // 建立開始日期
                this.Service.SearchEDate = "";  // 建立結束日期

                $("#SearchBlock #SearchDate").trigger('cancel.daterangepicker');
                $("#SearchBlock #SearchReset").addClass('none');
                this.Service.ListData();
            },
            // 搜尋:變更
            SearchChange() {
                if ($("#SearchBlock #SearchDate").val()!=""){
                    this.Service.SearchSDate = $("#SearchBlock #SearchDate").val().split(' ~ ')[0];
                    this.Service.SearchEDate = $("#SearchBlock #SearchDate").val().split(' ~ ')[1];
                }else{
                    this.Service.SearchSDate = "";
                    this.Service.SearchEDate = "";
                }
                $("#SearchBlock #SearchReset").removeClass('none');
            },
            // 詳情:開啟
            DetailOpen(No) {
                // 檢視權限
                if (this.Auth.MyBraUsersPower.BraPV != 1) {
                    alert("沒有檢視權限");
                    return false;
                }

                this.Editable = false;
                // API串接
                this.Service.DetailData(No).then(response => {
                    this.Service.MyFormData = response.data;
                    $("#editStaff").modal('show');
                }).catch(e => {
                    alert("取得資料失敗!");
                    console.log(e);
                });
            },
            // 新增:開啟
            AddOpen() {
                // 新增權限
                if (this.Auth.MyBraUsersPower.BraPA != 1) {
                    alert("沒有新增權限");
                    return false;
                }
                
                this.Service.MyFormData = new BraUsersForm();
                $("#addStaff").modal('show');
            },
            // 新增:送出
            AddSubmit() {
                // 新增權限
                if (this.Auth.MyBraUsersPower.BraPA != 1) {
                    alert("沒有新增權限");
                    return false;
                }
                
                let ERRORNo = 0;
                let ERROTxt = "";
                if (this.Service.MyFormData.BraUserAccount.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入員工編號";
                }
                if (this.Service.MyFormData.BraUserPassword.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入員工密碼";
                }
                if (this.Service.MyFormData.BraUserName.trim() == "") {
                    ERRORNo += 1;
                    ERROTxt += "請輸入員工姓名";
                }
                if (ERRORNo > 0) {
                    alert(ERROTxt);
                    return false;
                }
                // API串接
                this.Service.AddData().then(response => {
                    this.Service.MyFormDataEdit = response.data;
                    if (this.Service.MyFormDataEdit.Status == "SUCCESS") {
                        alert("新增成功!");
                        this.Service.ListData();
                    }
                }).catch(e => {
                    alert("新增失敗!");
                    console.log(e);
                });
            },
            // 編輯:開啟
            ModifyOpen(No) {
                // 編輯權限
                if (this.Auth.MyBraUsersPower.BraPM != 1) {
                    alert("沒有編輯權限");
                    return false;
                }

                this.Editable = true;
                // API串接
                this.Service.DetailData(No).then(response => {
                    this.Service.MyFormData = response.data;
                    $("#editStaff").modal('show');
                }).catch(e => {
                    alert("取得資料失敗!");
                    console.log(e);
                });
            },
            // 編輯:送出
            ModifySubmit() {
                // 編輯權限
                if (this.Auth.MyBraUsersPower.BraPM != 1) {
                    alert("沒有編輯權限");
                    return false;
                }
                
                // API串接
                this.Service.ModifyData().then(response => {
                    if (response.data.Status == "SUCCESS") {
                        this.Service.ListData();
                        $("#editStaff").modal('hide');
                    }
                });
            },
            //檢查:帳號
            CheckAccount() {
                let $this = this;
                let MyAccount = this.Service.MyFormData.BraUserAccount ?? "";
                //--判斷代碼可否使用
                clearTimeout(this.AccountTimeoutId);
                if (MyAccount) {
                    this.AccountTimeoutId = setTimeout(function () {
                        // API串接
                        $this.Service.AccountExists(MyAccount).then(response => {
                            if (response.data.Status == "ERROR") {
                                alert("不可使用!" + response.data.Message);
                            }
                        }).catch(e => {
                            alert("檢查失敗!" + e.Message);
                            console.log(e);
                        });
                    }, 1000) || null;
                }
            }
        },
        // 實體已建立，狀態與事件已初始化完成
        created() {
            // 取得目前分店編號
            this.PMSConfig.StoreNo = this.$route.params.branch;
            this.Service.StoreNo = this.$route.params.branch;
        },
        // 實體與掛載完成
        mounted() {
            let vthis = this;

            // 檢視權限
            if (this.Auth.MyBraUsersPower.BraPV != 1) {
                alert("沒有:檢視權限");
                this.$router.push({ path: "/", replace: true });
            }
            
            // 搜尋:日期 > 載入日期套件
            this.PMSKit.DatePicker($("#SearchBlock #SearchDate"), 'Range', 'YYYY-MM-DD');
            $("#SearchBlock #SearchDate").on('apply.daterangepicker', function(ev, picker) {
                vthis.SearchChange();
            });

            // 取得選項資料
            this.PMSConfig.OptionData();
            // 取得列表資料
            this.Service.ListData();
        }
    };
</script>